@use "../global/variables.scss";

.sec-media-gallery-grid {
  background-color: variables.$graySty6;
  .wrapper-box {
    .media-gallery-box {
      position: relative;

      .filter-bar {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 0 1.9rem;
        @media #{variables.$vpTablet991} {
          flex-direction: column-reverse;
          padding-bottom: 1.875rem;
        }

        .left-filter {
          flex: 0 0 60%;
          max-width: 60%;
          @media #{variables.$vpTablet991} {
            flex: 0 0 100%;
            max-width: 100%;
            margin-top: 1.5rem;
          }
          @media #{variables.$vpMobile600} {
            margin-top: 0;
          }

          .nav-pills {
            display: flex;
            gap: 1rem;

            @media #{variables.$vpMobile600} {
              flex-direction: row;
              overflow: scroll;
              flex-wrap: nowrap;
              padding: 1.5rem 0.25rem 1.5rem 1.25rem;
              margin: 0 -1.25rem;

              &::-webkit-scrollbar {
                display: none;
              }
            }

            .nav-item {
              display: flex;
              @media #{variables.$vpMobile600} {
                flex: 0 0 auto;
              }

              .nav-link {
                border-radius: 0.5rem;
                max-width: fit-content;
                padding: 0.98rem 1.5rem;
                line-height: normal;
                border: 0.75px solid rgba(0, 0, 0, 0.15);
                background-color: rgba(199, 152, 107, 0.1);
                color: variables.$blueSty1;
                backdrop-filter: blur(3.75px);
                transition: all 0.4s ease;
                display: flex;
                height: 3.5rem;
                align-items: center;
                margin-right: 0.5rem;
                cursor: pointer;
                text-transform: uppercase;
                font-family: variables.$secondaryFont;
                font-weight: 700;
                &:last-child {
                  margin-right: 0;
                }
                @media #{variables.$vpTablet1199} {
                  padding: 0.5rem 0.8rem;
                }
                @media #{variables.$vpTablet991} {
                  padding: 0.98rem 1.5rem;
                }

                @media #{variables.$vpMobile600} {
                  flex: 0 0 auto;
                  border-radius: 0.25rem;
                  height: 3rem;
                }

                &:hover {
                  background-color: rgba(169, 125, 70, 0.5);
                  @media #{variables.$vpTablet1199} {
                    background-color: rgba(199,152,107,.1);
                  }
                }
                &:focus {
                  outline: none;
                }

                &.active {
                  background-color: variables.$sunsetGoldSty2;
                  display: flex;
                  align-items: center;
                  gap: 0.5625rem;
                  color: #F6F0E8;

                  &::before {
                    content: "";
                    width: 0.482rem;
                    height: 0.482rem;
                    background-image: url("/assets/img/icon/daimondBg.svg");
                    background-repeat: no-repeat;
                    background-position: center;
                    display: block;
                    transition: all 0.4s ease;
                  }
                }
              }
            }
          }
        }

        .right-filter {
          display: flex;
          align-items: center;
          flex: 0 0 40%;
          max-width: 40%;
          justify-content: flex-end;
          gap: 0.5rem;
          @media #{variables.$vpTablet991} {
            justify-content: flex-start;
            flex: 0 0 100%;
            max-width: 100%;
          }
          @media #{variables.$vpMobile600} {
            width: 100%;
            padding-left: 0;
            flex-direction: column;
            gap: 1rem;
          }

          .search-box {
            width: 15.625rem;
            flex: 0 0 15.625rem;
            position: relative;
            input {
              &::placeholder {
                color: variables.$textStyleSecondary;
                font-size: 1.125rem;
                font-weight: 400;
                line-height: 1.2375rem;
                font-family: variables.$secondaryFont;
              }
              &::-ms-input-placeholder {
                color: variables.$textStyleSecondary;
              }
            }

            @media #{variables.$vpMobile600} {
              width: 100%;
              flex: 0 0 100%;
            }

            .btn-search {
              position: absolute;
              left: auto;
              top: 50%;
              right: 1.25rem;
              transform: translateY(-50%);
              border: 0;
              background-color: transparent;
              outline: 0;
              width: 1.5rem;
              height: 1.5rem;
              padding: 0;
              cursor: pointer;
              @media #{variables.$vpMobile600} {
                right: 0.5rem;
              }

              .serch-icon {
                width: 100%;
                height: 100%;
              }
            }
          }

          .filter-view-type {
            // width: 26.625rem;
            display: flex;
            gap: 0.5rem;
            // @media #{variables.$vpTablet991} {
            //     width: 100%;
            // }

            svg {
              width: 1.5rem;
              height: 1.5rem;
              transition: all 0.4s ease-in;
            }

            @media #{variables.$vpMobile600} {
              width: 100%;
            }

            .sort-box {
              width: 8rem;
              background: transparent;
              border-radius: 0.625rem;
              border: 1px solid rgba(0, 0, 0, 0.15);
              padding: 0.81rem 0.75rem;
              height: 3.5rem;
              display: flex;
              align-items: center;
              justify-content: space-between;
              cursor: pointer;
              transition: all 0.5s ease;
              
              

              @media #{variables.$vpMobile600} {
                width: 15rem;
                height: 3rem;
              }

              &.icon-rotate {
                .sort-icon {
                  transform: rotateX(180deg);
                  
                 
                }
              }
              p {
                color: variables.$blueSty1;
                transition: all 0.5s ease;
              }
              &:hover {
                background-color: variables.$sunsetGold;
                @media #{variables.$vpTablet1199} {
                  background-color: transparent;
                }
                .body1{
                  color: white;
                  @media #{variables.$vpTablet1199} {
                    color: variables.$blueSty1;
                  }
                }
                .sort-icon{
                  transition: all 0.5s ease;
                  path{
                    stroke: white;
                    transition: all 0.5s ease;
                    @media #{variables.$vpTablet1199} {
                      stroke: variables.$blueSty1;
                    }
                  }
                }
              }
            }

            .view-type {
              width: 7rem;
              background: transparent;
              border-radius: 0.625rem;
              border: 1px solid rgba(0, 0, 0, 0.15);
              height: 3.5rem;
              display: flex;
              align-items: center;
              justify-content: space-between;
              overflow: hidden;

              @media #{variables.$vpMobile600} {
                width: 6.81rem;
                height: 3rem;
              }

              ul {
                display: flex;
                align-items: center;
                justify-content: space-between;

                li {
                  padding-left: 0;
                  transition: all 0.4s ease;
                  &:hover {
                    background-color: rgba(169, 125, 70, 0.5);
                    @media #{variables.$vpTablet1199} {
                      background-color: transparent;
                    }
                  }
                  &::before {
                    display: none;
                  }
                  a {
                    width: 3.5rem;
                    padding: 1rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    @media #{variables.$vpMobile600} {
                      width: 3.4375rem;
                      height: 3.4375rem;
                      padding: 1.5rem 1rem;
                    }

                    &:hover {
                      // background-color: $secondarySimiLight;
                    }

                    &.active {
                      background-color: variables.$sunsetGoldSty2;
                      svg {
                        rect {
                          fill: white;
                        }
                      }
                    }

                    img {
                      width: 1.5rem;
                      height: 1.5rem;
                    }

                    &.grid-view-action {
                      &.active {
                        .list-view-listing {
                          display: none;
                        }
                        svg {
                          rect {
                            stroke: white;
                            fill: none;
                          }
                        }
                      }
                    }

                    &.list-view-action {
                      &.active {
                        .grid-view-listing {
                          display: none;
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .input-field {
            background: transparent;
            // border: 0.063rem solid $secondarySimiLight;
            padding: 1rem 3rem 1rem 0.75rem;
            width: 100%;
            border-radius: 0.625rem;
            border: 1px solid rgba(0, 0, 0, 0.15);
            height: 3.5rem;
            padding-right: 3.5rem;
            font-family: variables.$secondaryFont;
            color: #464647;
            @media #{variables.$vpMobile600} {
              height: 3rem;
              padding: 0.75rem 3rem 0.75rem 0.75rem;
            }
            &::placeholder {
              color: #464647;
            }
          }
        }
      }

      .tab-content-wrapper {
        .tab-content {
          .filter-panel {
            .media-listing {
              &.grid-view-listing {
                position: relative;
                display: flex;
                flex-wrap: wrap;
                margin: 0 -0.31rem;
                justify-content: flex-start;
                // gap: 0.62rem;

                @media #{variables.$vpMobile600} {
                  margin: 0;
                }

                .listingWrapper {
                  // flex: 0 0 32.561%;
                  width: 32.5%;
                  margin: 0;
                  margin: 0.6rem 0.31rem 0;
                  background: transparent;
                  // width: auto;
                  height: auto;
                  @media #{variables.$vpTablet1199} {
                    width: 32.29%;
                  }
                  @media #{variables.$vpTablet991} {
                    // flex: 0 0 32.25%;
                    max-width: 32.25%;
                  }
                  @media #{variables.$vpMobile600} {
                    // flex: 0 0 100%;
                    max-width: 100%;
                    // padding: 0.5rem 0;
                    margin: 0.625rem 0 0;
                    width: 100%;
                  }

                  .grid-view-item {
                    position: relative;
                    border-radius: 1rem;
                    overflow: hidden;
                    width: 100%;
                    height: 100%;
                    @media #{variables.$vpTablet1199} {
                      // height:34.25rem;
                    }
                    @media #{variables.$vpTablet991} {
                      width: 100%;
                      // height: 36rem;
                    }
                    @media #{variables.$vpMobile600} {
                      width: 100%;
                      // height: 26rem;
                      border-radius: 0.5rem;
                    }
                    .content_box {
                      @media #{variables.$vpMobile600} {
                        gap: 0.25rem;
                      }
                      .list_tag {
                        display: none;
                      }
                      .decription_wrapper {
                        .content__wrap {
                          display: flex;
                          flex-direction: column;
                          gap: 1rem;
                          @media #{variables.$vpMobile600} {
                            gap: 0.5rem;
                          }
                          .meta_wrapper {
                            flex-direction: column;
                            gap: 0.25rem;
                            @media #{variables.$vpMobile600} {
                              flex-direction: row;
                              justify-content: space-between;
                              align-items: center;
                            }
                            .bottom_tagline {
                              .card_meta {
                                display: none;
                                &:first-child {
                                  display: block;
                                }
                              }
                            }
                            .action_btn {
                              display: none;
                              .btn_wrap {
                                .btn_Icon {
                                  @media #{variables.$vpMobile600} {
                                    padding: 0.75rem;

                                    p {
                                      display: none;
                                    }
                                    .download_iconBox {
                                      width: 1rem;
                                      height: 1rem;
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }

                    &.list-view-item {
                      display: none;
                    }
                  }
                }
              }
              &.list-view-listing {
                display: flex;
                // gap: 2rem;
                flex-direction: column;
                flex-wrap: wrap;
                @media #{variables.$vpMobile600} {
                  gap: 0.625rem;
                }

                .listingWrapper {
                  flex: 0 0 100%;
                  max-width: 100%;
                  margin: 0;
                  padding: 0.625rem 0 0;
                  background: transparent;
                  width: 100%;
                  height: auto;
                  // .list-items {
                  //     &.grid-view-item {
                  //         display: none !important;
                  //     }
                  // }

                  @media #{variables.$vpMobile600} {
                    padding: 0;
                    &:first-child {
                      padding-top: 0.625rem;
                    }
                  }
                  .grid-view-item {
                    display: flex !important;
                    align-items: center;
                    width: 100%;
                    // border: 0.063rem solid $secondarySemiLight;
                    border-radius: 1rem;
                    overflow: hidden;
                    cursor: pointer;
                    height: 14.375rem;

                    @media #{variables.$vpMobile600} {
                      border-radius: 0.5rem;
                      padding: 0.5rem;
                      align-items: flex-start;
                      gap: 0;
                      height: auto;
                      background-color: white;
                    }
                    .tag {
                      display: none;
                    }
                    .img-sec {
                      border-radius: 1rem 0 0 1rem;
                      width: 26.25rem;
                      flex: 0 0 26.25rem;
                      @media #{variables.$vpTablet1199} {
                        height: 16.3rem;
                        width: 18.25rem;
                        flex: 0 0 18.25rem;
                      }
                      @media #{variables.$vpMobile600} {
                        height: 5.875rem;
                        max-width: 23.6%;
                        flex: 0 0 23.6%;
                        border-radius: 0.5rem;
                      }
                      img {
                        border-radius: 1rem 0 0 1rem;
                        @media #{variables.$vpMobile600} {
                          border-radius: 0.5rem;
                        }
                      }
                      .tag {
                        display: none;
                      }
                    }
                    .content_box {
                      gap: 0.5rem;
                      border-radius: 0 1rem 1rem 0;
                      display: flex;
                      width: 67.5%;
                      flex-direction: column;
                      align-items: flex-start;
                      @media #{variables.$vpTablet1199} {
                        width: 73%;
                      }
                      @media #{variables.$vpMobile600} {
                        width: 100%;
                      }
                      .main-title {
                        .title {
                          padding-top: 0.5rem;
                          @media #{variables.$vpMobile600} {
                            padding-top: 0.25rem;
                          }
                          .card_main_title {
                            // @media #{variables.$vpMobile600} {
                            //    font-size: 1rem;
                            //    line-height: 1.1rem;
                            //   }
                          }
                        }
                      }
                      //   align-items: center;
                      //   justify-content: space-between;
                      .content_box__left {
                        max-width: 70%;
                        flex: 0 0 70%;
                        @media #{variables.$vpTablet1199} {
                          max-width: 60%;
                          flex: 0 0 60%;
                        }
                        @media #{variables.$vpMobile600} {
                          max-width: 100%;
                          flex: 0 0 100%;
                        }
                      }
                      .decription_wrapper {
                        flex-direction: row;
                        @media #{variables.$vpMobile600} {
                          flex-direction: column;
                        }
                        .content__wrap {
                          @media #{variables.$vpMobile600} {
                            gap: 0.25rem;
                            display: flex;
                            flex-direction: column;
                          }
                        }
                        .action_btn {
                          max-width: 30%;
                          flex: 0 0 30%;
                          flex-direction: column;
                          align-items: flex-end;
                          .btn_wrap{
                            .btn{
                              &:hover{
                                @media #{variables.$vpTablet1199} {
                                  background: transparent !important;
                                }
                              }
                            }
                          }
                          @media #{variables.$vpTablet1199} {
                            max-width: 40%;
                            flex: 0 0 40%;
                          }
                          @media #{variables.$vpMobile600} {
                            max-width: max-content;
                            flex: 0 0 max-;
                            gap: 0.25rem;
                            width: 100%;
                          }
                        }
                      }

                      @media #{variables.$vpMobile600} {
                        max-width: 100%;
                        // flex: 0 0 76.4%;
                        gap: 0.25rem;
                        padding: 0 1rem;
                        flex-direction: column;
                      }
                      .pera-sec {
                        max-width: 75%;
                        @media #{variables.$vpMobile600} {
                          max-width: 100%;
                        }
                      }
                      .meta_wrapper {
                        justify-content: space-between;
                        align-items: flex-end;
                        // margin-top: 1.63rem;
                        @media #{variables.$vpMobile600} {
                          align-items: center;
                        }
                        .bottom_tagline {
                          margin-top: 3.31rem;
                          @media #{variables.$vpMobile600} {
                            margin-top: 0.94rem;
                          }
                          .card_meta {
                            display: flex;
                            svg {
                              width: 1.125rem;
                              height: 1.125rem;
                              margin-right: 0.25rem;
                              @media #{variables.$vpMobile600} {
                                width: 0.875rem;
                                height: 0.875rem;
                              }
                              path {
                                fill: #5d5e5e;
                              }
                            }
                          }
                        }
                        .action_btn {
                          .btn_wrap {
                            .btn_Icon {
                              @media #{variables.$vpMobile600} {
                                padding: 0.75rem;
                                p {
                                  display: none;
                                }
                                .download_iconBox {
                                  width: 1rem;
                                  height: 1rem;
                                }
                              }
                            }
                          }
                        }
                      }
                    }

                    // &.grid-view-item {
                    //     display: none !important;
                    // }
                  }
                }
              }
            }
          }
        }
      }
      .btn_box {
        padding-top: 2.5rem;
        display: flex;
        justify-content: center;
        @media #{variables.$vpMobile600} {
          padding-top: 2rem;
        }
        .btn {
          padding: 0.98rem 3rem;
          @media #{variables.$vpMobile600} {
            padding: 0.75rem 2rem;
          }
        }
      }
      .no_search_result {
        display: none;
      }
      .sec-title {
        margin-bottom: 1.9rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.375rem;
        }
      }
      &.no_result {
        .tab-content-wrapper {
          display: none;
        }
        .no_search_result {
          display: block;
          padding: 9.32rem 0 9.47rem;
          max-width: 28.9375rem;
          margin: 0 auto;
          @media #{variables.$vpMobile600} {
            padding: 8rem 0 7rem;
          }
          @media #{variables.$vpTablet991} {
            max-width: 100%;
          }
          .content_box {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 1.125rem;
            flex-direction: column;
            text-align: center;
            .no_result_title {
              color: variables.$blueSty1;
            }
            .pera_text {
              color: #2e2f2f;
            }
          }
        }
      }
    }
  }

   
  &.media-galleryWrapper {
    position: relative;
    z-index: 2;
    min-height: 100vh;
    .media-gallery-box {
      .filter-bar{
        padding: 0 0 2.69rem 0;
        @media #{variables.$vpMobile600} {
          padding: 0 0 1.875rem 0;
        }
        .left-filter{
          .nav-pills{
            @media #{variables.$vpMobile600} {
              padding: 2rem 0 1.875rem 0;
            }
            .nav-item{
              .nav-link{
                @media #{variables.$vpMobile600} {
                  height: 3.5rem;
                  padding: .98rem 2.371rem;
                }
              }
            }
          }
        }
        .right-filter{
          @media #{variables.$vpMobile600} {
            flex-direction: row;
            gap: 0.5rem;
          }
          .search-box{
            width: 18.313rem;
            flex: 0 0 18.313rem;
            @media #{variables.$vpMobile600} {
              width: 17.875rem;
              flex: 0 0 17.875rem;
            }
            input{
              @media #{variables.$vpMobile600} {
                height: 3.5rem;
              }
            }
          }
          .filter-view-type{
            .sort-box{
              width: 10.75rem;
              @media #{variables.$vpMobile600} {
                width: 3.5rem;
                height: 3.5rem;
                justify-content: center;
              }
              p{
                @media #{variables.$vpMobile600} {
                  display: none;
                }
              }
            }
          }
        }
      }
      .btn_box {
        padding-top: 1.88rem;
        @media #{variables.$vpMobile600} {
          padding-top: 1.38rem;
        }
      }
    }
  }
}

// filter modal
.modal {
  .modal-content {
    padding: 5rem 2.5rem 3rem;
    
    @media #{variables.$vpMobile600} {
      padding: 3.625rem 1.25rem 2.5rem;
    }
    .filterWrapper {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      margin: 0 0 0 auto;
      position: relative;
      z-index: 9;

      .filters-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 0.5rem;
        @media #{variables.$vpMobile600} {
          padding-bottom: 0.69rem;
          }
          .btnWrap{
            .btn{
              &:hover{
                @media #{variables.$vpTablet1199} {
                  background: transparent !important;
                }
              }
            }
          }
        }
      }
      .inner_filter_wrapper {
        .customScroll_bar {
          height: calc(100vh - 20rem);
          // @media #{variables.$vpDesktop1440} {
          //     max-height: 56.25rem;
          // }

          .filterModal_tags {
            .filter_tags_wrapper {
              padding-top: 2.5rem;
              display: flex;
              flex-direction: column;
              gap: 1rem;
              @media #{variables.$vpMobile600} {
                padding-top: 2rem;
              }
              .tags_wrapper {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                flex-wrap: wrap;
                .tag-label {
                  background: rgba(199, 152, 107, 0.1);
                  border: 0.75px solid rgba(0, 0, 0, 0.15);
                  backdrop-filter: blur(3.75px);
                  border-radius: 0.5rem;
                  padding: 0.98rem 1.5rem;
                  font-weight: bold;
                  color: #0d2640;
                  cursor: pointer;
                  transition: all 0.5s ease;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  height: 3.5rem;

                  @media #{variables.$vpMobile600} {
                    height: 3rem;
                  }
                  &:hover {
                    background-color: variables.$sunsetGold;
                    @media #{variables.$vpTablet1199} {
                      background-color: rgba(199,152,107,.1);
                    }
                    span{
                      color: white;
                      transition: all 0.5s ease;
                      @media #{variables.$vpTablet1199} {
                        color: #0d2640;
                      }
                    }
                    @media #{variables.$vpMobile600} {
                      background-color: rgba(199, 152, 107, 0.1);
                    }
                  }
                  &:has(input[type="checkbox"]:checked) {
                    background: #a97d46;
                    color: #fff;
                  }

                  input {
                    display: none;
                  }
                  span {
                    text-transform: uppercase;
                    font-size: 1rem;
                    font-weight: 700;
                    color: variables.$blueSty1;
                    transition: all 0.35s ease;
                  }
                  input:checked + span {
                    position: relative;
                    margin-left: 0.25rem;
                    color: white;
                    &::before {
                      position: absolute;
                      content: "";
                      width: 0.35rem;
                      height: 0.35rem;
                      background-color: white;
                      left: -1rem;
                      top: 50%;
                      transform: translateY(-50%) rotate(45deg);
                      opacity: 1;
                      scale: 1;
                    }
                  }
                }
              }
            }
          }
        }
        .bottom_btnWrap {
          // position: absolute;
          // bottom: 3rem;
          // left: 50%;
          // transform: translateX(-50%);
          display: flex;
          justify-content: center;
          padding-top: 2rem;
          .btn-primary {
            margin: 0 auto;
            @media #{variables.$vpMobile600} {
              width: max-content;
            }
          }
        }
      }
    }
  
  &.filter_modal {
    .modal-content {
      @media #{variables.$vpMobile600} {
        padding: 6.25rem 1.25rem 6rem;
      }
    }
  }
}

.mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
  background-color: rgba(207, 164, 116, 1);
  width: 2px;
}
.mCS-dark.mCSB_scrollTools .mCSB_draggerRail {
  background-color: white;
}
