@use "../global/variables.scss";

.opportunity-sec-dark {
  z-index: 1;
  position: relative;
  .sub-content-sec {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 2.5rem;
    max-width: 35.37rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
      flex-direction: column;
      max-width: 100%;
    }
    .heading-title {
      flex: 0 0 100%;
      max-width: 100%;
      color: white;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 0;
      }
    }
    .para {
      flex: 0 0 39%;
      max-width: 39%;
      color: variables.$textStyleSecondary;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }
  .opportunity-slider-dark {
    margin: 0 -0.625rem !important;
    @media #{variables.$vpMobile600} {
      padding-right: 2.813rem;
    }

    .opportunity-card {
      backdrop-filter: blur(4.571428298950195px);
      margin-inline: 0.625rem;
      height: 18.188rem;
      border-radius: 12px;
      border: 0.762px solid rgba(255, 255, 255, 0.4);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      position: relative;
      border-radius: 12px;
      overflow: hidden;
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(255, 255, 255, 0.4);
        transition: opacity 0.35s ease, background 0.35s ease;
        @media #{variables.$vpMobile600} {
          opacity: 0;
        }
      }
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        background: linear-gradient(
            180deg,
            rgba(0, 0, 0, 0) 49.84%,
            rgba(0, 0, 0, 0.8) 100%
          ),
          linear-gradient(
            180deg,
            rgba(0, 0, 0, 0.5) 1.77%,
            rgba(0, 0, 0, 0) 57.23%
          );
        transition: opacity 0.35s ease, background 0.35s ease;
        @media #{variables.$vpMobile600} {
          opacity: 1;
        }
      }

      .bg-img {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        opacity: 0;
        transition: all ease 0.35s;
        @media #{variables.$vpMobile600} {
          opacity: 1;
        }
        .card-img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }
      }
      .text-sec {
        padding: 1.375rem 1.5rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        z-index: 1;
        @media #{variables.$vpTablet1199} {
          padding: 1.5rem;
        }
        @media #{variables.$vpMobile600} {
          padding: 1.5rem;
        }
        .heading {
          color: white;
          transition: all ease 0.35s;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
          @media #{variables.$vpMobile600} {
            color: white;
          }
        }
        .para {
          color: variables.$graySty3;
          transition: all ease 0.35s;
          @media #{variables.$vpMobile600} {
            color: white;
          }
        }
      }
      &:hover {
        &::after {
          opacity: 1;
        }
        &::before {
          opacity: 0;
        }
        .bg-img {
          opacity: 1;
        }
        .text-sec {
          .heading {
            color: white;
          }
          .para {
            color: white;
          }
        }
      }
    }
    .slick-list {
      overflow: visible !important;
      .slick-slide {
        &:before {
          background: rgba($color: variables.$white, $alpha: 0.1);
        }
        @media #{variables.$vpMobile600} {
        .bg-img {
          opacity: 0;
        }
        &:hover {
          .text-sec {
            .heading, .para {
              color: #0d2640;
              @media #{variables.$vpMobile600} {
                color: variables.$white;
              }
            }
          }
        }
        &.slick-active {
          .bg-img {
              opacity: 1;
            }
            .text-sec {
              .heading, .para {
                color: variables.$white !important;
              }
            }
            @media #{variables.$vpMobile600} {
              opacity: 1 !important;
            }
          }
        }
      } 
    }
  }
  .custom-arrows {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.5rem;
    column-gap: 1.5rem;

    @media #{variables.$vpMobile600} {
      justify-content: space-between;
      margin-top: 2rem;
    }
    .slide-info {
      display: flex;
      align-items: center;
      column-gap: 0.3rem;
      /*rtl:raw:flex-direction: row-reverse;*/
      .current-slide {
        color: variables.$sunsetGoldSty2;
      }
      .total-slides {
        color: variables.$backgroundLight;
      }
      .sep-slide {
        color: variables.$backgroundLight;
      }
    }
    .arrows-wrapper {
      display: flex;
      column-gap: 0.5rem;
      @media #{variables.$vpMobile600} {
        column-gap: 0.25rem;
      }
      .custom-prev,
      .custom-next {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.75px solid rgba(255, 255, 255, 0.15);
        background: rgba(255, 255, 255, 0.1);
        border-radius: 50%;
        overflow: hidden;
        width: 3rem;
        height: 3rem;
        /*rtl:raw:transform: rotate(-180deg);*/
        cursor: pointer;
        transition: all ease 0.35s;
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg {
          width: 1rem;
          height: 1rem;
          path {
            transition: all ease 0.35s;
            fill: white;
          }
        }
        &:hover {
          border: 0.7px solid variables.$sunsetGoldSty2;
          svg {
            path {
              fill: variables.$sunsetGoldSty2;
            }
          }
        }
        /* Disable hover effects below 600px */
        @media #{variables.$vpTablet1199} {
          &:hover {
            border: .75px solid hsla(0, 0%, 100%, .15);
            svg {
              path {
                fill: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
}
