@use "../global/variables.scss";

.ceo-message {
  position: relative;
  z-index: 2;
  color: white;
  sup {
    padding-left: 5rem;
    padding-right: 0.5rem;
    @media #{variables.$vpTablet991} {
      padding-left: 0.688rem;
    }
  }
  sub {
    padding-left: 0.5rem;
  }
  .wrapper {
    padding: 10.375rem 0;
    padding-bottom: 0;
    display: flex;
    flex-direction: row;

    @media #{variables.$vpTablet991} {
      flex-direction: column;
      align-items: center;
    }

    @media #{variables.$vpMobile600} {
      padding-top: 4.875rem;
    }

    .image-warpper {
      width: 15.25rem;
      height: 15.25rem;
      flex-shrink: 0;
      border-radius: 50%;
      border: 2px solid white;
      overflow: hidden;
      position: relative;
      @media #{variables.$vpMobile600} {
        flex-shrink: 0;
        height: 8.75rem;
        width: 8.75rem;
      }
      picture {
        height: 100%;
        width: 100%;
        top: 0;
        position: absolute;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .content-warpper {
      margin-left: 11.81rem;

      @media #{variables.$vpTablet1199} {
        margin-left: 2rem;
      }
      @media #{variables.$vpTablet991} {
        margin-left: 0;
        margin-top: 1.87rem;
      }
      .heading4 {
        span {
          color: variables.$sunriseGold;
        }
        .sup-icon {
          vertical-align: 1rem;
          @media #{variables.$vpMobile600} {
            vertical-align: 0.5rem;
          }
          .qoma-icon {
            /*rtl:raw:transform: scaleX(-1);*/
          }
        }
        .sub-icon {
          vertical-align: -1.5rem;
          @media #{variables.$vpMobile600} {
            vertical-align: -0.5rem;
          }
          .qoma-icon {
            /*rtl:raw:transform: scaleX(-1);*/
          }
        }
      }
      .inner-content {
        margin-top: 2rem;
        @media #{variables.$vpMobile600} {
          margin-top: 1.5rem;
        }
        .ceo-name {
          margin-bottom: 0.5rem;
          @media #{variables.$vpMobile600} {
            margin-bottom: 0.25rem;
          }
        }
      }

      .btn-wrapper {
        display: flex;
        flex-direction: row;
        margin-top: 2rem;
        gap: 0.87rem;
        @media #{variables.$vpMobile600} {
          margin-top: 1.19rem;
          gap: 0.5rem;
        }
      }
    }
  }
  .vertical-line-wrapper {
    position: relative;
    margin-top: 2.688rem;
  }
  &.quoted--msg {
    .wrapper {
      padding: 12rem 0;
      @media #{variables.$vpMobile600} {
        padding: 6.81rem 0;
      }
    }
  }
}
