@use "../global/variables.scss";

.sec-interactive-width {
  position: relative;
  z-index: 1;
  padding-top: 18rem;
  padding-bottom: 10.15rem;
  @media #{variables.$vpMobile600} {
    padding-top: 15.5rem;
    padding-bottom: 5.5rem;
  }
  .comp-wrapper {
    overflow: hidden;

    .img-gridBox {
      border-radius: 0.75rem;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      margin-top: 2.38rem;

      @media #{variables.$vpMobile600} {
        flex-direction: column;
        margin-top: 2rem;
        border-radius: 1rem;
      }

      .img-grid {
        max-width: 50%;
        flex: 0 0 50%;
        position: relative;
        // border-right: 0.125rem solid white;
        transition: all 0.8s ease;

        @media #{variables.$vpMobile600} {
          max-width: 100% !important;
          flex: 0 0 100% !important;
          border-right: 0;
        }

        .img-wrapper {
          height: 30.476rem;
          transition: all 0.8s ease;

          @media #{variables.$vpTablet1199} {
            height: 24rem;
          }

          @media #{variables.$vpMobile600} {
            height: 15rem;
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          .icon-box {
            display: none;
            width: 3rem;
            height: 3rem;
            position: absolute;
            top: 1.25rem;
            right: 1.25rem;
            z-index: 1;
            border: 1px solid white;
            border-radius: 50%;
            overflow: hidden;
            @media #{variables.$vpTablet991} {
              display: block;
              &::after {
                content: "";
                width: 1rem;
                height: 1.5px;
                border-radius: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background: white;
              }
              &::before {
                content: "";
                width: 1.5px;
                height: 1rem;
                border-radius: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background: white;
                transition: all ease 0.35s;
              }
            }
          }
        }

        .content-wrapper {
          position: absolute;
          bottom: 0;
          padding: 3.19rem 3.06rem;
          max-width: 32rem;
          color: white;
          z-index: 1;
          transition: all 0.8s ease;

     
          @media #{variables.$vpTablet991} {
            max-width: 28rem;
            padding: 1.25rem;
          }
     

          .content-heading {
            transition: all 0.8s ease;
          }
          .inner-content-wrapper {
            transform: translateY(20px);
            max-height: 0;
            overflow: hidden;
            transition: all 0.8s ease;
            opacity: 0;
            @media #{variables.$vpMobile600} {
              padding-top: 0;
            }
          }
        }

        &.expand {
          max-width: calc(57.82% - 2px);
          flex: 0 0 57.82%;

          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            background: linear-gradient(
                180deg,
                rgba(0, 0, 0, 0) 61.72%,
                rgba(0, 0, 0, 0.6) 100%
              ),
              linear-gradient(
                0deg,
                rgba(0, 0, 0, 0.3) 0%,
                rgba(0, 0, 0, 0.3) 100%
              );
            width: 100%;
            height: 100%;
            transition: all 0.8s ease;
          }

          .content-wrapper {
            .content-heading {
              transition: all 0.8s ease;
              padding-bottom: 1rem;
              @media #{variables.$vpMobile600} {
                padding-bottom: 0.5rem;
              }
            }
            .inner-content-wrapper {
              transform: translateY(0);
              max-height: 6rem;
              opacity: 1;
              transition: all 0.5s ease;

              .main-secondary-btn {
                span {
                  color: white;
                }

                img {
                  filter: invert(1);
                }

                &:hover {
                  img {
                    filter: invert(0);
                  }
                }
              }
            }
          }
          .img-wrapper {
            .icon-box {
              &::before {
                content: "";
                width: 1.5px;
                height: 0.875rem;
                border-radius: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                opacity: 0;
                transform: translate(-50%, -50%) rotate(90deg);
                background: white;
              }
            }
          }

          @media #{variables.$vpMobile600} {
            max-width: 100%;
            flex: 0 0 100%;
          }
        }

        &.contract {
          max-width: 42.18%;
          flex: 0 0 42.18%;
          border-right: 0;
          @media #{variables.$vpMobile600} {
            max-width: 100%;
            flex: 0 0 100%;
          }
          .img-wrapper {
            
            .icon-box {
              @media #{variables.$vpMobile600} {
                display: block;
              }
            }
          }
        }
        // &.img-left {
        //   img {
        //     object-position: left;
        //   }
        // }
        // &.img-right {
        //   img {
        //     object-position: right;
        //   }
        // }
      }
      .seprator-white {
        height: 30.476rem;
        width: 2px;
        background: white;
        @media #{variables.$vpTablet1199} {
          height: 24rem;
        }
        @media #{variables.$vpMobile600} {
          width: 100%;
          height: 1px;
        }
      }
    }
  }
}
