@use "../global/variables.scss";

.faq-sec {
  min-height: 100vh;
  .sec-padded{
    @media #{variables.$vpMobile600} {
      padding-top: 0;
    }
  }
  .faq-accordion-wrapper {
    z-index: 2;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
    }
    .filter-sec {
      flex: 0 0 32.3%;
      max-width: 32.3%;
      padding-left: 5rem;
      @media #{variables.$vpTablet1199} {
        padding-left: 2.5rem;
      }
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 3.75rem;
        background: variables.$graySty6;
        padding: 1.25rem;
        z-index: 1;
      }

      .search-faq {
        position: relative;
        margin-bottom: 2.5rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.25rem;
        }

        .input-txt-field {
          border-radius: 0.625rem;
          border: 1px solid rgba(0, 0, 0, 0.15);
          overflow: hidden;
          background: transparent;
          padding: 0.8125rem 0.75rem;
          color: variables.$textStyleSecondary;
          width: 100%;
          /*rtl:raw:text-align: right;*/
          &:focus-visible {
            outline: 1px solid variables.$sunsetGoldSty2;
          }
        }

        .search-icon {
          position: absolute;
          width: 1.5rem;
          height: 1.5rem;
          top: 50%;
          right: 0.75rem;
          transform: translateY(-50%);
          @media #{variables.$vpMobile600} {
            width: 1.5rem;
            height: 1.5rem;
          }

          svg {
            width: 100%;
            height: 100%;
          }
        }
      }

      .filter-tabs {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
        @media #{variables.$vpMobile600} {
          flex-wrap: nowrap;
          overflow: auto;
          white-space: nowrap;
          margin-right: -1.25rem;
          padding-bottom: 0.5rem;
          // padding-inline: 1.25rem;
        }

        .tab {
          min-width: fit-content;
          background: rgba(199, 152, 107, 0.1);
          border: 0.75px solid rgba(0, 0, 0, 0.15);
          padding-block: 1.12rem;
          padding-inline: 2rem;
          border-radius: 8px;
          overflow: hidden;
          width: fit-content;
          transition: all ease 0.35s;
          @media #{variables.$vpMobile600} {
            display: inline-block;
            border-radius: 4px;
          }
          p {
            color: variables.$blueSty1;
            transition: all ease 0.35s;
            text-transform: uppercase;
          }
          .cta-txt {
            font-size: 1rem;
            line-height: 1.4;
          }
          &:hover {
            background: variables.$sunsetGoldSty2;
            border: 0.75px solid rgba(255, 255, 255, 0.15);
            cursor: pointer;
            @media #{variables.$vpMobile600} {
              background: rgba(199, 152, 107, 0.1);
              border: 0.75px solid rgba(0, 0, 0, 0.15);
            }
            p {
              color: white;
              @media #{variables.$vpMobile600} {
                color: variables.$blueSty1;
              }
            }
          }
          &.active {
            background: variables.$sunsetGoldSty2;
            border: 0.75px solid rgba(255, 255, 255, 0.15);
            cursor: pointer;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: flex-start;

            &::before {
              position: absolute;
              content: "";
              background: url("/assets/img/icon/diamondIcon.svg");
              background-repeat:no-repeat;
              width: 0.688rem;
              height: 0.7rem;
              left: 1.5rem;
              top: 50%;
              transform: translateY(-50%);
              opacity: 1;
              scale: 1;
              animation: iconAppear 0.35s ease forwards;
            }
            p {
              color: white;
              transform: translateX(8px);
            }
          }
        }
      }
    }
    .accordion-sec {
      flex: 0 0 57.66%;
      max-width: 57.66%;
      padding-right: 5rem;
      @media #{variables.$vpTablet1199} {
        padding-right: 2.5rem;
      }
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        width: 100%;
        padding-inline: 1.25rem;
      }
      .accordion-block {
        margin-bottom: 5rem;
        &:last-child {
          margin-bottom: 0;
        }
        .accordion-title {
          color: variables.$blueSty1;
          margin-bottom: 1.5rem;
          @media #{variables.$vpMobile600} {
            margin-bottom: 1rem;
          }
        }
        .accordion {
          width: 100%;
          @media #{variables.$vpMobile600} {
            flex: 0 0 100%;
            max-width: 100%;
          }

          .accordion-item {
            background: variables.$graySty6;
            border-radius: 12px;
            padding: 1.25rem;
            overflow: hidden;
            color: variables.$blueSty1;
            min-height: 0;
            margin-bottom: 1rem;
            @media #{variables.$vpMobile600} {
              margin-bottom: 0.5rem;
            }

            .accordion-content {
              max-height: 0;
              padding-top: 0;
              overflow: hidden;
              color: variables.$textStyleSecondary;
              transition: max-height 0.35s ease, padding-top 0.35s ease; // Apply transition to both max-height and padding-top
              p {
                color: variables.$textStyleSecondary;
                font-size: 1rem;
                font-weight: 400;
                line-height: 1.15;
                @media #{variables.$vpMobile600} {
                  font-size: 1rem;
                }
              }
            }

            .accordion-header {
              border: none;
              width: 100%;
              cursor: pointer;
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding-bottom: 0;
              transition: all ease 0.35s;
              .accordion-heading {
                color: variables.$blueSty1;
                padding-right: 2rem;
              }

              .accordion-icon {
                width: 3rem;
                height: 3rem;
                min-width: 3rem;
                border: 0.643px solid rgba(0, 0, 0, 0.15);
                border-radius: 50%;
                overflow: hidden;
                position: relative;
                &::after {
                  content: "";
                  width: 14px;
                  height: 2px;
                  border-radius: 2px;
                  position: absolute;
                  top: 50%;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  background: variables.$blueSty1;
                }
                &::before {
                  content: "";
                  width: 2px;
                  height: 14px;
                  border-radius: 2px;
                  position: absolute;
                  top: 50%;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  background: variables.$blueSty1;
                  transition: all ease 0.35s;
                }
              }

              &.active {
                &.accordion-header {
                  padding-bottom: 1rem;
                  @media #{variables.$vpMobile600} {
                    padding-bottom: 0.5rem;
                  }
                }
                & + .accordion-content {
                  max-height: 20rem; // Set a value high enough to fit content
                }
                .accordion-icon {
                  &::before {
                    content: "";
                    width: 2px;
                    height: 14px;
                    border-radius: 2px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    opacity: 0;
                    transform: translate(-50%, -50%) rotate(90deg);
                    background: variables.$blueSty1;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
@keyframes iconAppear {
  0% {
    opacity: 0;
    transform: translateY(-50%) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translateY(-50%) scale(1);
  }
}
