@use "../global/variables.scss";

.csr-slider-sec {
  background: white;
  .tabs-wrapper {
    position: relative;
    &::after {
      content: "";
      background-color: white;
      height: 104%;
      left: -5rem;
      position: absolute;
      top: -5px;
      width: 57%;
      z-index: 2;
      @media #{variables.$vpMobile600} {
        display: none;
      }
    }
    .header-sec {
      max-width: 50%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 4;
      @media #{variables.$vpMobile600} {
        position: unset;
        max-width: 100%;
        margin-bottom: 2.5rem;
      }
      .heading3 {
        margin-bottom: 2rem;
        max-width: 32.5rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.5rem;
        }
      }
      .tabs {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
        @media #{variables.$vpMobile600} {
          flex-wrap: nowrap;
          overflow: auto;
          white-space: nowrap;
          margin-right: -1.25rem;
          padding-bottom: 0.5rem;
          // padding-inline: 1.25rem;
        }
        .tab {
          min-width: fit-content;
          background: rgba(199, 152, 107, 0.1);
          border: 0.75px solid rgba(0, 0, 0, 0.15);
          padding-block: 1rem;
          padding-inline: 2rem;
          border-radius: 8px;
          overflow: hidden;
          width: fit-content;
          transition: all ease 0.35s;
          @media #{variables.$vpMobile600} {
            display: inline-block;
            border-radius: 4px;
          }
          p {
            color: variables.$blueSty1;
            transition: all ease 0.35s;
            text-transform: uppercase;
          }
          .cta-txt {
            font-size: 1rem;
            line-height: 1.4;
          }
          &:hover {
            background: variables.$sunsetGoldSty2;
            border: 0.75px solid rgba(255, 255, 255, 0.15);
            cursor: pointer;
            @media #{variables.$vpMobile600} {
              background: rgba(199, 152, 107, 0.1);
              border: 0.75px solid rgba(0, 0, 0, 0.15);
            }
            p {
              color: white;
              @media #{variables.$vpMobile600} {
                color: variables.$blueSty1;
              }
            }
          }
          &.active {
            background: variables.$sunsetGoldSty2;
            border: 0.75px solid rgba(255, 255, 255, 0.15);
            cursor: pointer;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: flex-start;

            &::before {
              position: absolute;
              content: url("/assets/img/icon/diamondIcon.svg");
              width: 0.688rem;
              height: 1.1rem;
              left: 1.5rem;
              top: 50%;
              transform: translateY(-50%);
              opacity: 1;
              scale: 1;
              animation: iconAppear 0.35s ease forwards;
            }
            p {
              color: #f6f0e8;
              transform: translateX(8px);
            }
          }
        }
      }
    }

    .tab-content {
      .tab-pane {
        display: none;
        &.active {
          display: block;
        }
      }
    }

    .slider-wrapper {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 40.188rem;
      @media #{variables.$vpMobile600} {
        flex-direction: column-reverse;
        height: auto;
        row-gap: 2.5rem;
      }
      .text-wrapper {
        flex: 0 0 50.8%;
        max-width: 50.8%;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        z-index: 3;
        padding-right: 8rem;
        height: 100%;
        @media #{variables.$vpMobile600} {
          flex: 0 0 100%;
          max-width: 100%;
          padding-right: 0;
        }
        .text-slider {
          .text-slide {
            .main-heading {
              margin-bottom: 1.5rem;
              transition: opacity 1s ease-in-out;
              color: variables.$blueSty1;
            }
            .para {
              margin-bottom: 1.5rem;
              transition: opacity 1s ease-in-out;
              color: variables.$blueSty1;
            }
          }
          .slick-prev,
          .slick-next {
            background-color: transparent;
            border: none;
            font-size: 2rem;
            cursor: pointer;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }
          .slick-prev {
            left: -30px;
          }
          .slick-next {
            right: -30px;
          }
        }
        .btn-sec {
          display: flex;
          justify-content: space-between;
          align-items: center;
          @media #{variables.$vpMobile600} {
            flex-direction: column;
            row-gap: 2rem;
          }
        }
      }

      .image-slider {
        flex: 0 0 42.66%;
        max-width: 42.66%;
        z-index: 1;
        @media #{variables.$vpMobile600} {
          flex: 0 0 100%;
          max-width: 100%;
        }
        .image-slide {
          height: 40.188rem;
          scale: 0.784;
          opacity: 0.2;
          transition: all ease 0.35s;
          border-radius: 16px;
          overflow: hidden;
          @media #{variables.$vpMobile600} {
            height: 100%;
            height: 18.75rem;
            scale: 0.9;
            
            border-radius: 12px;
          }
          &.slick-active {
            scale: 1;
            opacity: 1;
            border-radius: 24px;
            @media #{variables.$vpMobile600} {
              border-radius: 12px;
              width: 20.625rem !important;
            }
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .slick-list {
          overflow: visible;
        }
      }
    }
  }
}

.slider-controls {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  column-gap: 1.5rem;

  @media #{variables.$vpMobile600} {
    justify-content: space-between;
    width: 100%;
  }
  .slider-counter {
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    column-gap: 0.3rem;
    color: rgba(13, 38, 64, 0.2);
    /*rtl:raw:flex-direction: row-reverse;*/
    @media #{variables.$vpMobile600} {
      font-size: 1.25rem;
    }
    .current-slide {
      color: variables.$sunsetGoldSty2;
    }
    .total-slides {
      color: rgba(13, 38, 64, 0.2);
    }
    .sep-slide {
      color: rgba(13, 38, 64, 0.2);
    }
  }
  .arrows-wrapper {
    display: flex;
    column-gap: 0.5rem;
    @media #{variables.$vpMobile600} {
      column-gap: 0.25rem;
    }

    .custom-arrow {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 0.7px solid rgba(0, 0, 0, 0.15);
      background: rgba(199, 152, 107, 0.1);
      border-radius: 50%;
      overflow: hidden;
      width: 3rem;
      height: 3rem;
      /*rtl:raw:transform: rotate(-180deg);*/
      cursor: pointer;
      transition: all ease 0.35s;
      @media #{variables.$vpTablet1199} {
        &:hover {
          background: rgba(199, 152, 107, 0.1) !important;
        }
      }
      @media #{variables.$vpMobile600} {
        width: 2.5rem;
        height: 2.5rem;
      }
      svg {
        width: 1rem;
        height: 1rem;
        path {
          transition: all ease 0.35s;
        }
      }
      &:hover {
        border: 0.7px solid variables.$sunsetGoldSty2;
        svg {
          path {
            fill: variables.$sunsetGoldSty2;
          }
        }
      }
      /* Disable hover effects below 600px */
      @media #{variables.$vpTablet1199} {
        &:hover {
          border: 0.7px solid rgba(0, 0, 0, 0.15);

          svg {
            path {
              fill: variables.$blueSty1 !important;
            }
          }
        }
      }
    }
  }
}
