@use "../global/variables.scss";

.sec-footer {
    position: relative;
    background: linear-gradient(0deg, #9e7140 19.58%, #cca374 91.99%), #000;
    &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: url("/assets/img/bg-elements/dots.png");
        background-repeat: repeat;

    }
    .footer--wrapper {
        position: relative;
        z-index: 2;
        .footer-linking {
            display: flex;
            align-items: flex-start;
            @media #{variables.$vpTablet991} {
                flex-direction: column;
            }
            .footer-logo {
                flex: 0 0 13.516%;
                max-width: 13.516%;
                @media #{variables.$vpTablet991} {
                    flex: 0 0 25%;
                    max-width: 25%;
                }
                @media #{variables.$vpMobile600} {
                    flex: 0 0 100%;
                    max-width: 100%;
                }
                svg {
                    width: 100%;
                    height: 100%;
                    @media #{variables.$vpMobile600} {
                        width: 7.16169rem;
                        height: 3.875rem;
                    }
                }
            }
            .footer-links--wrapper {
                display: flex;
                padding-left: 8.75rem;
                flex: 0 0 86.484%;
                max-width: 86.484%;
                @media #{variables.$vpTablet1199} {
                    padding-left: 6.5rem;
                }
                @media #{variables.$vpTablet991} {
                    padding-left: 0;
                    padding-top: 2rem;
                    flex: 0 0 100%;
                    max-width: 100%;
                    width: 100%;
                }
                @media #{variables.$vpMobile600} {
                    flex-direction: column-reverse;
                    padding-right: 1.25rem;
                }
                .linking-box {
                    flex: 0 0 50%;
                    max-width: 50%;
                    display: flex;
                    justify-content: space-between;
                    ul {
                        padding-bottom: 4.94rem;
                        @media #{variables.$vpMobile600} {
                            padding-bottom: 3rem;
                        }
                        li {
                            padding: 1rem 0;
                            a {
                                position: relative;
                                &::after {
                                    content: "";
                                    position: absolute;
                                    left: 0;
                                    bottom: -0.2rem;
                                    width: 0;
                                    height: 1px;
                                    background: variables.$white;
                                    transition: width 0.35s ease;
                                }
                            }
                            @media #{variables.$vpMobile600} {
                                padding: 0.75rem 0;
                            }
                            &:first-child {
                                padding-top: 0;
                            }
                            &:last-child {
                                padding-bottom: 0;
                            }
                            
                            &:hover {
                                a {
                                    &:after {
                                        width: 100%;
                                        @media #{variables.$vpTablet1199} {
                                            width: 0;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    @media #{variables.$vpMobile600} {
                        flex: 0 0 100%;
                        max-width: 100%;
                        .vertical-line-footer {
                            display: none;
                        }
                    }
                }
                .connect-box {
                    flex: 0 0 41.68%;
                    max-width: 41.68%;
                    padding-left: 4.19rem;
                    @media #{variables.$vpMobile600} {
                        flex: 0 0 100%;
                        max-width: 100%;
                        padding-left: 0;
                        padding-bottom: 2rem;
                    }
                    .sec-heading {
                        color: variables.$white;
                        max-width: 16.375rem;;
                    }
                    .social-links {
                        display: flex;
                        align-items: flex-start;
                        margin: 1rem 0;
                        .social-logo {
                            border-radius: 50%;
                            border: 0.75px solid rgba(255, 255, 255, 0.15);
                            background: rgba(199, 152, 107, 0.10);
                            padding: 0.909rem;
                            display: flex;
                            align-items: center;
                            margin-right: 0.5rem;
                            transition: all 0.5s ease;
                            @media #{variables.$vpMobile600} {
                                padding: 0.66rem;
                            }
                            img,svg {
                                width: 1.06rem;
                                height: 1.06rem;
                            }
                            &:hover {
                                background:rgba(255, 255, 255, 0.15);
                            }
                        }
                    }
                    .getInTouch {
                        text-align: center;
                    }
                }
            }
        }
        .footer-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 2.5rem;
            .brand-logo {
                img,svg {
                    width: 14.35rem;
                    height: auto;
                }
            }
            .copyright {
                opacity: 0.7;
                p {
                    color: variables.$white;
                }
            }
            .terms {
                ul {
                    display: flex;
                    li {
                        padding: 0 0.75rem;
                        @media #{variables.$vpMobile600} {
                            padding: 0 0.5rem;
                        }
                        &:first-child {
                            padding-left: 0;
                        }
                        &:last-child {
                            padding-right: 0;
                        }
                        a {
                            position: relative;
                            letter-spacing: 0.035rem;
                            text-transform: uppercase;
                            &:after {
                                content: "";
                                position: absolute;
                                left: 0;
                                bottom: 0;
                                width: 0;
                                height: 1px;
                                background: variables.$white;
                                transition: width 0.35s ease;
                            }
                            &:hover {
                                &:after {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
            }
            @media #{variables.$vpMobile600} {
                flex-direction: column;
                align-items: flex-start;
                margin-top: 3rem;
                .terms {
                    order: 2;
                    margin: 2rem 0;
                }
                .copyright {
                    order: 3;
                }
            }
        }
    }
    .horizontal-line {
        margin-top: 0.3rem;
    }
}

.vertical-line-footer {
    width: 1px;
    height: 0%;
    background-color: white;
    position: relative;
    z-index: 2;
  
    .icon-1 {
      position: absolute;
      left: -0.25rem;
      top: -2px;
      will-change: transform;
      transform: translateZ(0); 
    }
    .icon-2 {
      position: absolute;
      left: -0.25rem;
      top: 10%;
      will-change: transform;
      transform: translateZ(0);
    }
  }


  .line--mbl {
    display: none;

    @media #{variables.$vpMobile600} {
        display: block;
        position: absolute;
        height: calc(100% + 4rem);
        right: 0;
        bottom: 0;
        top: 4rem;
    }
  }
  .footer-btn {
    .btn {
        padding-top: 2px;
        transition: all 0.35s ease;
        width: 21rem;
        justify-content: center;
        height: 2rem;
        border: 1.25px solid rgba(255, 255, 255, 0.30);
        @media #{variables.$vpMobile600} {
            width: 100%;
        }
        &:hover {
            background: hsla(0, 0%, 100%, .15);
            @media #{variables.$vpTablet1199} {
                background: transparent !important;
            }
        }
    }
  }
  .copyright--wrapper {
    display: flex;
    align-items: center;
    @media #{variables.$vpMobile600} {
        flex-direction: column;
        align-items: flex-start;
    }
    .copyright {
        margin-right: 7.19rem;
        @media #{variables.$vpTablet1199} {
            margin-right: 2.5rem;
        }
        @media #{variables.$vpMobile600} {
            margin-right: 0;
        }
    }
  }
  .footer-element {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }