@use "../global/variables.scss";

.sec-value-dark {
  position: relative;
  z-index: 2;
  .value-slider-text-mob {
    display: none;
    @media #{variables.$vpMobile600} {
      margin-bottom: 1rem;
      display: block;
    }
    .slider-heading {
      color: white;
      text-transform: uppercase;
    }
    
  }
  .tabs-container-dark {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5rem;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 0;
    }

    .slider-tabs {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      list-style: none;
      padding: 0;
      margin-bottom: 0;
      flex: 0 0 39.5%;
      max-width: 39.5%;
      .value-slider-text {
        margin-bottom: 1.313rem;
        .slider-heading {
          color: white;
          text-transform: uppercase;
        }
        @media #{variables.$vpMobile600} {
          margin-bottom: 1rem;
        }
      }
      .tab {
        position: relative;
        cursor: pointer;
        transition: background-color 0.3s;
        width: fit-content;
        color: white;
        margin-bottom: 2.188rem;
        &:last-child {
          margin-bottom: 0;
        }

        .bottom-active {
          width: 0;
          background-color: variables.$sunsetGoldSty2;
          height: 2px;
          position: absolute;
          bottom: -0.938rem;
          left: 0;
          transition: width 0.3s ease, background-color 0.3s ease;
          visibility: hidden;
          .icon-start {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -2px;
          }
          .icon-center {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 10%;
          }
          .icon-end {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: -2px;
          }
        }
        &.active,
        &:hover {
          color: variables.$sunsetGoldSty2;
          .bottom-active {
            width: 100%;
            visibility: visible;
            svg {
              path {
                fill: variables.$sunsetGoldSty2;
              }
            }
          }
        }
      }
    }

    .select2-container {
      transition: all 1s ease;

      .custom-select2-container {
        .select2-selection {
          position: relative;
          padding-right: 30px;
        }

        .select2-selection__arrow {
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          pointer-events: none;
          transition: transform 0.3s ease;
          width: 16px;
          height: 16px;
          background-image: url("/assets/img/dropdown-arrow.svg");
          background-size: contain;
          background-repeat: no-repeat;
        }
      }

      .custom-select2-selection {
        font-family: variables.$primaryFont !important;
        /*rtl:raw:font-family: "Zawi"*/
        font-size: 1.5rem;
        color: variables.$sunsetGoldSty2;
        border: 0;
        width: 100% !important;
        display: block;
        position: relative;
        transition: all 0.35s ease;
        &::before {
          content: " ";
          width: 100%;
          background-color: variables.$sunsetGoldSty2;
          height: 2px;
          position: absolute;
          bottom: -0.625rem;
          left: 0;
          transition: all 1s ease;
        }
        &::after {
          content: url("/assets/img/icon/dropdown-arrow.svg");
          width: 1rem;
          height: auto;
          position: absolute;
          right: -1.625rem;
          top: 50%;
          transform: translateY(-50%);
          transition: transform 0.5s;
        }
        .bottom-active-mob {
          width: 100%;
          // background-color: variables.$sunsetGoldSty2;
          // height: 2px;
          position: absolute;
          bottom: -10px;
          left: 0;
          transition: width 0.3s ease, background-color 0.3s ease;
          .icon-start {
            position: absolute;
            top: -6px;
            left: -2px;
          }
          .icon-center {
            position: absolute;
            top: -6px;
            left: 12px;
          }
          .icon-end {
            position: absolute;
            top: -6px;
            right: -2px;
          }
        }
      }
      &.select2-container--open {
        .custom-select2-selection {
          font-family: variables.$primaryFont !important;
          /*rtl:raw:font-family: "Zawi"*/
          &::after {
            width: 1rem;
            height: auto;
            position: absolute;
            transform: translateY(-50%) rotateX(180deg);
          }
        }
      }
    }

    .custom-select2-dropdown {
      background: #000;
      border-radius: 12px;
      overflow: hidden;
    }

    .custom-select2-dropdown .select2-results__option--highlighted,
    .custom-select2-dropdown .select2-results__option--selected {
    }

    .tabs-content {
      z-index: 9;
      flex: 0 0 52.1%;
      max-width: 52.1%;
      min-height: 24.438rem;
      max-height: 24.438rem;
      position: relative;
      height: 100%;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-top: 3.75rem;
      }
      .tab-content {
        opacity: 0;
        visibility: hidden;
        height: 0;
        transition: opacity 0.3s ease-in-out, visibility 0.3s;
        overflow: hidden;
        scale: 0;
        &.active {
          opacity: 1;
          scale: 1;
          visibility: visible;
          height: 100%;
          transition: opacity 0.3s ease-in-out, visibility 0.3s;
        }

        .slick-slider {
          display: block;
          position: relative;

          .slick-list {
            padding-left: 2.313rem;
            @media #{variables.$vpMobile600} {
              padding-top: 2.313rem;
              padding-left: 0;
            }
          }

          .slick-slide {
            text-align: center;
            height: 100%;
            font-size: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 8px;
            transform-origin: center left;
            z-index: 1 !important;
            opacity: 1 !important;
            transform: scale(0.8) translateX(-2rem);
            transition: all 0.5s ease 0s !important;
            @media #{variables.$vpMobile600} {
              transform: scale(0.85) translateY(-2.5rem);
              transform-origin: center top;
            }

            &.slick-active {
              opacity: 1 !important;
              z-index: 3 !important;
              position: relative;
              transform: scale(1) translateX(0);
            }
            &.slick-next {
              z-index: 2 !important;
              transform: scale(0.9) translateX(-1rem);
              @media #{variables.$vpMobile600} {
                transform: scale(0.95) translateY(-1.25rem);
                transform-origin: center top;
              }
            }
            &.img-slide {
              max-height: 24.438rem;
              min-height: 24.438rem;
              border-radius: 12px;
              overflow: hidden;
              padding-top: 20.68%;
              @media #{variables.$vpMobile600} {
                width: 100%;
                max-height: 15rem;
                min-height: 15rem;
              }
              picture {
                height: 100%;
                top: 0;
                position: absolute;
                width: 100%;
                img {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }
                ::after {
                  content: "";
                  position: absolute;
                  width: 100%;
                  height: 100%;
                  background: linear-gradient(
                      180deg,
                      rgba(0, 0, 0, 0) 59.08%,
                      rgba(0, 0, 0, 0.4) 100%
                    ),
                    linear-gradient(
                      0deg,
                      rgba(0, 0, 0, 0.2) 0%,
                      rgba(0, 0, 0, 0.2) 100%
                    );

                  left: 0;
                  top: 0;
                }
              }
              .img-content {
                position: absolute;
                bottom: 0;
                left: 0;
                padding-bottom: 1.375rem;
                padding-left: 2rem;
                max-width: 78%;
                @media #{variables.$vpMobile600} {
                  padding: 1.25rem;
                }
                p {
                  text-align: left;
                  color: white;
                }
              }
            }
          }

          .slick-next-slide {
            transform: scale(0.95) translateX(-1.25rem);
            z-index: 2 !important;
            transform-origin: center left;
          }
        }
        .custom-arrows-pagination {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          margin-top: 2.75rem;
          column-gap: 1.5rem;
          @media #{variables.$vpMobile600} {
            justify-content: space-between;
            margin-top: 2.5rem;
          }
          .slide-count {
            display: flex;
            align-items: center;
            column-gap: 0.3rem;
            /*rtl:raw:flex-direction: row-reverse;*/
            .current-slide {
              color: variables.$sunsetGoldSty2;
            }
            .total-slides {
              color: variables.$graySty3;
            }
            .sep-slide {
              color: variables.$graySty3;
            }
          }
          .arrows-wrapper {
            display: flex;
            column-gap: 0.5rem;
            @media #{variables.$vpMobile600} {
              column-gap: 0.25rem;
            }
            .custom-prev-arrow,
            .custom-next-arrow {
              display: flex;
              align-items: center;
              justify-content: center;
              border: 0.75px solid rgba(255, 255, 255, 0.15);
              background: rgba(255, 255, 255, 0.1);
              border-radius: 50%;
              overflow: hidden;
              width: 3rem;
              height: 3rem;
              /*rtl:raw:transform: rotate(-180deg);*/
              cursor: pointer;
              transition: all ease 0.35s;
              @media #{variables.$vpMobile600} {
                width: 2.5rem;
                height: 2.5rem;
              }
              svg {
                width: 1rem;
                height: 1rem;
                path {
                  transition: all ease 0.35s;
                  fill: white;
                }
              }
              &:hover {
                border: 0.7px solid variables.$sunsetGoldSty2;
                @media #{variables.$vpTablet1199} {
                  background: rgba(255, 255, 255, 0.1) !important;
                  border: none !important;
                }
                svg {
                  path {
                    fill: variables.$sunsetGoldSty2;
                  }
                }
              }
              /* Disable hover effects below 600px */
              @media #{variables.$vpTablet1199} {
                &:hover {
                  border: .75px solid hsla(0, 0%, 100%, .15) !important;
                  svg {
                    path {
                      fill: variables.$blueSty1;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .custom-select2-dropdown {
    display: block;
    .select2-results {
      .select2-results__options {
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(12px);
        border-radius: 12px;
        overflow: hidden;
        padding: 1.5rem;
        overflow: visible;
        z-index: 9;
        position: relative;
        .select2-results__option--selectable {
          padding-bottom: 0.625rem;
          padding-top: 0.625rem;
          border-bottom: 0.5px solid rgba(207, 164, 116, 0.2);
          &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
          }
          &:first-child {
            padding-top: 0;
          }
        }
      }
    }
  }
  .select2-search--dropdown {
    display: none !important;
  }
}
