@use "../global/variables.scss";

.cookie-banner {
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #f9f7f3;
  color: #2c2e34;
  padding: 1.25rem 2rem;
  border-radius: 0.5rem;
  box-shadow: 0px 4px 64px 0px rgba(51, 51, 51, 0.1);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 2.5rem;
  width: calc(100% - 5rem);
  z-index: 1000;
  gap: 2rem;

  @media #{variables.$vpMobile600} {
    padding: 1.5rem 1.25rem;
    align-items: flex-start;
    flex-direction: column;
    max-width: 100%;
    margin: 1.25rem;
    width: calc(100% - 2.5rem);
  }

  &.hide {
    display: none;
  }

  .cookie-heading {
    flex: 1;
    max-width: 40%;

    @media #{variables.$vpMobile600} {
      max-width: 100%;
    }
  }

  .cookie-buttons {
    display: flex;
    gap: 0.5rem;

    @media #{variables.$vpMobile600} {
      gap: 0.25rem;
    }

    .cookie-btn {
      padding: 10px 20px;
      border-radius: 20px;
      border: 1px solid #333;
      background-color: transparent;
      cursor: pointer;
      font-size: 12px;
      font-weight: bold;
      transition: all 0.3s ease;

      &.accept {
        background-color: #333;
        color: #fff;
        border: none;

        &:hover {
          background-color: #555;
        }
      }

      &.decline:hover {
        background-color: #eee;
      }
    }
  }
}
