@use "../global/variables.scss";

.press-sec {
  overflow: hidden;
  .sub-content-sec {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.5rem;

    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
    .sub-heading {
      max-width: 30.25rem;
      @media #{variables.$vpMobile600} {
        max-width: 12.5rem;
      }
      .heading-title {
        color: variables.$blueSty1;
      }
      .special-heading {
        color: variables.$blueSty1;
        margin-bottom: 1rem;
        text-transform: uppercase;
        @media #{variables.$vpMobile600} {
          margin-bottom: 0.5rem;
        }
      }
    }
  }
  .press-card-slider {
    margin: 0 -0.313rem !important;
    @media #{variables.$vpMobile600} {
      margin: 0 !important;
      padding-right: 1.25rem;
    }
    .media-card {
      margin-right: 0.625rem;
      height: 100%;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.4);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      position: relative;
      border-radius: 16px;
      overflow: hidden;
      @media #{variables.$vpMobile600} {
        margin: 0 0.625rem 0 0;
      }
      .badge {
        position: absolute;
        left: 1rem;
        top: 1.438rem;
        background: variables.$graySty6;
        border-radius: 8px;
        overflow: hidden;
        padding: 0.35rem .5rem .2rem;
        z-index: 1;
        @media #{variables.$vpTablet1199} {
          top: 1rem;
        }
        .badge-txt {
          color: rgba(70, 70, 71, 0.8);
          text-transform: uppercase;
        }
      }
      .img-sec {
        height: 14.375rem;
        overflow: hidden;
        @media #{variables.$vpTablet1199} {
          height: 12rem;
        }
        .card-img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
          transition: all ease 0.35s;
        }
      }
      .text-sec {
        padding: 1.5rem;
        background: #fbf8f7;
        @media #{variables.$vpTablet1199} {
          padding: 1rem;
        }
        .heading5 {
          color: variables.$blueSty1;
          margin-bottom: 1rem;
          @media #{variables.$vpTablet1199} {
            margin-bottom: 0.5rem;
          }
        }
        .heading {
          margin-bottom: 1rem;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          color: #464647;
        }
        .para {
          color: rgba(70, 70, 71, 0.8);
          text-transform: uppercase;
        }
      }
      &:hover {
        .img-sec {
          .card-img {
            scale: 1.1;
          }
        }
      }
      &.card_with_noImg{
        .text-sec{
          display: flex;
          justify-content: space-between;
          height: 100%;
          flex-direction: column;
        }
      }
    }
    .slick-list {
      overflow: visible !important;
    }
  }
  .custom-arrows-press {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.5rem;
    column-gap: 1.5rem;

    @media #{variables.$vpMobile600} {
      justify-content: space-between;
      margin-top: 2rem;
    }
    .slide-count {
      display: flex;
      align-items: center;
      column-gap: 0.3rem;
      /*rtl:raw:flex-direction: row-reverse;*/
      .current-slide-press {
        color: variables.$sunsetGoldSty2;
      }
      .total-slides-press {
        color: rgba(13, 38, 64, 0.2);
      }
      .sep-slide {
        color: rgba(13, 38, 64, 0.2);
      }
    }
    .arrows-wrapper {
      display: flex;
      column-gap: 0.5rem;
      @media #{variables.$vpMobile600} {
        column-gap: 0.25rem;
      }
      .custom-prev-press,
      .custom-next-press {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.7px solid rgba(0, 0, 0, 0.15);
        background: rgba(199, 152, 107, 0.1);
        border-radius: 50%;
        overflow: hidden;
        width: 3rem;
        height: 3rem;
        /*rtl:raw:transform: rotate(-180deg);*/
        cursor: pointer;
        transition: all ease 0.35s;
        @media #{variables.$vpTablet1199} {
          &:hover {
            background: rgba(199, 152, 107, 0.1) !important;
          }
        }
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg {
          width: 1rem;
          height: 1rem;
          path {
            transition: all ease 0.35s;
          }
        }
        &:hover {
          border: 0.7px solid variables.$sunsetGoldSty2;
          svg {
            path {
              fill: variables.$sunsetGoldSty2;
              @media #{variables.$vpTablet1199} {
                fill: #292D32 !important;
              }
            }
          }
        }
        /* Disable hover effects below 600px */
        @media #{variables.$vpTablet1199} {
          &:hover {
            border: 0.7px solid rgba(0, 0, 0, 0.15);

            svg {
              path {
                fill: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
  &.sty-01 {
   position: relative;
   z-index: 1;
    .sub-heading {
      max-width: 100%;
      @media #{variables.$vpMobile600} {
        max-width: 12.5rem;
      }
    }
    .press-card-slider {
      .media-card {
        // height: 26.375rem;
        background: white;
        .text-sec {
          // background: white;
          .heading5 {
            margin-bottom: 0;
          }
          .description-wrapper {
            
            .description {
              color: rgba(70, 70, 71, 0.8);
              padding-top: 1rem;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;
              text-overflow: ellipsis;
            }
            .para {
              padding-top: 0.5rem;
              color: rgba(70, 70, 71, 0.8);
            }
          }
        }
        &.card_with_noImg {
          background: white;
          .text-sec {
            .heading {
              padding-top: 2.31rem;
            }
            .description-wrapper{
              padding-top: 12.0652rem;
              @media #{variables.$vpTablet1199} {
                padding-top: 9.69rem;
              }
            }
          }
        }
      }
    }
  }
}
