@use "../global/variables.scss";

.colorBar--wrapper {
    display: flex;
    flex-wrap: wrap;
}
.colorBar {
    width: 50%;
    height: 4rem;
    &.clr1 {
        background: variables.$green;
    }
    &.clr2 {
        background: variables.$yellow;
    }
    &.clr3 {
        background: variables.$sunriseGold;
    }
    &.clr4 {
        background: variables.$sunsetGoldSty1;
    }
    &.clr5 {
        background: variables.$sunsetGoldSty2;
    }
    &.clr6 {
        background: variables.$blueSty1;
    }
    &.clr7 {
        background: variables.$blueSty2;
    }
    &.clr8 {
        background: variables.$blueSty3;
    }
    &.clr9 {
        background: variables.$coralSty1;
    }
    &.clr10 {
        background: variables.$coralSty2;
    }
    &.clr11 {
        background: variables.$graySty1;
    }
    &.clr12 {
        background: variables.$graySty2;
    }
    &.clr13 {
        background: variables.$graySty3;
    }
    &.clr14 {
        background: variables.$graySty4;
    }
    &.clr15 {
        background: variables.$graySty5;
    }
}