@use "../global/variables.scss";

.detail_banner_content{
  padding-top: 16.19rem;
  // padding-top: 14.94rem;

  @media #{variables.$vpMobile600} {
    padding-top: 4.88rem;
  }

  .inner-banner-contentWrapper{
    padding-bottom: 5rem;
    @media #{variables.$vpMobile600} {
      padding-top: 0.25rem;
      padding-bottom: 2.5rem;
    }
    p{
      padding-top: 1.5rem;
      @media #{variables.$vpMobile600} {
        padding-top: 1rem;
        font-size: 1rem;
        line-height:  1.1rem;
      }
    }
    .body1{
      margin: 0 0 2rem;
      p{
          color: variables.$blueSty1;;
          &:last-child{
              margin: 0 0 2rem 0;
          }
          img{
              margin: 1rem 0 0.5rem 0;
              border-radius: 0.5rem;
          }
          .img-credits{
              font-size: 0.7rem;
              line-height: 0.945rem;
              font-weight: 300;
              
          }
          a{
            margin-top: 0;
          }
      }
      ol{
          margin: 1.5rem 0 0 0;
          padding:  0 0 0 1rem;
          list-style: auto;
          li{
              padding-bottom: 0;
              color: variables.$blueSty1;

          }
      }
      h2, h3, h4, h5, h6{
          padding:  1.5rem 0 0 0;
          color: variables.$blueSty1;
      }
      ul{
          margin: 1.5rem 0 0 0;
          padding:  0 0 0 1rem;
          // list-style: auto;
          li{
              padding-bottom: 0;
              color: variables.$blueSty1;
              position: relative;
              &::before{
                content: '';
                position: absolute;
                top: 50%;
                left: -1rem;
                width: 0.35rem;
                height: 0.35rem;
                background-color: #CFA474;
                z-index: 1;
                transform: translateY(-50%) rotate(45deg);

              }

          }
      }
      .download_btnWrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2.5rem 0 0.81rem;
        @media #{variables.$vpMobile600} {
          padding: 2rem 0 0.29rem 0;
        }
        .title_box{
          @media #{variables.$vpMobile600} {
            max-width: 10rem;
          }
          .heading5{
            padding-top: 0;
          }
        }
        .btn_right{
          .btn_Icon{
            background: rgba(199,152,107,.1);
            margin-top: 0;
            color: variables.$blueSty1;
            font-size: 1rem;
            font-weight: 700;
            @media #{variables.$vpMobile600} {
              font-size: 0.75rem;
            }
            &:hover {
              svg {
                path {
                  stroke: variables.$white;
                  transition: all 0.5s ease;
                  @media #{variables.$vpTablet1199} {
                    stroke: rgb(13, 38, 64);
                  }
                }
              }
              &::after{
                display: none;
              }
            }
          }
        }
      }
      blockquote{
          margin: 1.5rem 0 0;
          padding:  1rem 2rem;
          position: relative;
          font-weight: 400;
          font-size: 1.5rem;
          line-height: 1.725rem;
          color: variables.$blueSty1;
          a{
            font-size: 1.5rem;
            line-height: 1.725rem;
            margin-top: 0;
            @media #{variables.$vpMobile600} {
              font-size: 1.25rem;
              line-height: 1.4375rem;
            }
          }
          span{
            color: variables.$sunriseGold;
          }
          @media #{variables.$vpMobile600} {
            padding: 0.5rem 1.5rem;
            margin: 1rem 0 0;
            font-size: 1.25rem;
            line-height: 1.4375rem;
          }
          &::before{
              content: '';
              position: absolute;
              top: 0;
              // left: 0;
              left: 0;
              width: 0.125rem;
              height: 100%;
              background-color: rgba(207, 164, 116, 1);
          }
          .quote-author{
              // display: block;
              margin: 1rem 0 0 0; 
              font-size: 1.125rem;
              line-height: 1.2375rem;
              font-weight: 400;
              padding-top: 0;
              color: variables.$textStyleSecondary;
              font-family: variables.$secondaryFont;
              @media #{variables.$vpMobile600} {
                margin: 0.75rem 0 0 0; 
                font-size: 1rem;
                line-height: 1.1rem;
                padding-top: 0 !important;
              }
          }
      }
    
     
     
     } 
     .bottom-wrapper{
        .horizontal-line{
          background-color: rgba(207, 164, 116, 1);
          span{
            svg{
              path{
                fill: rgba(207, 164, 116, 1);
              }
            }
          }
        }
     }
     .share-wrapper{
      padding: 2.81rem 0 0 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media #{variables.$vpMobile600} {
        padding: 2.34rem 0 0 0;
      }
      h5{
          
          color: variables.$blueSty1;
      }
      .social-icons{
          display: flex;
          justify-content: center;
          .icon-box{
            border: 0.75px solid rgba(0, 0, 0, 0.15);
            border-radius: 9.375rem;
            margin: 0 .5rem 0 0;
            transition: all .3s ease;
            background: rgba(199, 152, 107, 0.10);
            &:hover {
              background: var(--action-primary-hover, #AA865E) !important;
              a{
                &::after{
                  display: none;
                }
              }
             
              svg {
                path {
                  fill: rgba(255, 255, 255, 1);
                }
                &.img-link {
                  path {
                    stroke: rgba(255, 255, 255, 1);
                  }
                }
              }
              @media #{variables.$vpTablet1199} {
                background: rgba(199, 152, 107, 0.10) !important;
                svg {
                  path {
                    fill: rgba(13, 38, 64, 1);
                  }
                  &.img-link {
                    path {
                      stroke: rgba(13, 38, 64, 1);
                    }
                  }
                }
              }
             
            }
            @media #{variables.$vpMobile600} {
              margin: 0 .25rem 0 0;
            }
              &:last-child{
                margin: 0;
              }
              a{
                width: 3rem;
                height: 3rem;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 0;
                @media #{variables.$vpMobile600} {
                  width: 2.5rem;
                  height: 2.5rem;
                }
                svg{
                    width: 1rem;
                    height: auto;
                    display: block;
                    overflow: visible;
                    path{
                      fill: rgba(13, 38, 64, 1);
                    }
                }
              }
              // &:hover{
              //     border-color: var(--bs-mintGreen-600);
              // }
          }
      }
  }
  }
  &.terms-and-condition{
    padding: 0;
   .inner-banner-contentWrapper{
    max-width: 100%;
   }
  }
}