@use "./variables";

p {
	font-size: 1.125rem;
	font-weight: 400;
	line-height: 1.1;
	margin: 0;
}

.body1 {
	font-size: 1.125rem;
	font-weight: 400;
	line-height: 1.1;
	@media #{variables.$vpMobile600} {
		font-size: 1rem;
	}
}
.body2 {
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.15;
	@media #{variables.$vpMobile600} {
		font-size: 0.875rem;
	}
}
.body3 {
	font-size: 0.875rem;
	font-weight: 400;
	line-height: 1.2;
	@media #{variables.$vpMobile600} {
		font-size: 0.75rem;
	}
}

.cta-txt {
	font-size: 1rem;
	font-weight: bold;
	line-height: 1.15;
	@media #{variables.$vpMobile600} {
		font-size: 0.875rem;
	}
}
.overline {
	font-size: 1.125rem;
	font-weight: 400;
	line-height: 1.15;
	@media #{variables.$vpMobile600} {
		font-size: 1rem;
	}
}
.label {
	font-size: 0.875rem;
	font-weight: 400;
	line-height: 1.15;
	@media #{variables.$vpMobile600} {
		font-size: 0.75rem;
	}
}
blockquote{
	font-family: variables.$primaryFont;
}


h1,
h2,
h3,
h4,
h5,
h6,
.heading1,
.heading2,
.heading3,
.heading4,
.heading5,
.heading6 {
	margin: 0;
	font-family: variables.$primaryFont;
	line-height: 1.15;
	font-weight: 400;
	/*rtl:raw:font-family: "Zawi"*/
}

h1,
.heading1 {
	font-size: 8.125rem;
	@media #{variables.$vpMobile600} {
		font-size: 4rem;
	}
}

h2,
.heading2 {
	font-size: 4rem;
	@media #{variables.$vpMobile600} {
		font-size: 2rem;
	}
}

h3,
.heading3 {
	font-size: 3rem;
	@media #{variables.$vpMobile600} {
		font-size: 1.75rem;
	}
}

h4,
.heading4 {
	font-size: 2rem;
	@media #{variables.$vpMobile600} {
		font-size: 1.5rem;
	}
}

h5,
.heading5 {
	font-size: 1.5rem;
	@media #{variables.$vpMobile600} {
		font-size: 1.25rem;
	}
}