@use "../global/variables.scss";

.press_card_sec{
  position: relative;
  z-index: 1;
  .title-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title--text{
      color: white;
    }
  }
  .press_grid{
    display: flex;
    justify-content: flex-start;
    gap: 0.63rem;
    flex-wrap: wrap;
    padding-top: 2rem;
    .card_box{
      .content_box{
        gap: 0.5rem;
        .decription_wrapper{
          .bottom_tagline{
            padding-top: 0.5rem;
          }
        }
      }
    }
   
  }
  &.report-cards{
    background: none;
    position: relative;
    z-index: 1;
    .title-wrap{
      .title--text{
        color: variables.$blueSty1;
      }
    }
    .press_grid{
      .card_box{
        .content_box{
          background-color: white;
        }
      }
      .card_listing{
        .listing_card_box{
          background-color: white;
        }
      }
    }
  }
  &.report-cards{
    .press_grid{
      .card_listing{
        .listing_card_box{
          .content_wrapper{
            .tag{
              background-color: variables.$graySty6;
            }
          }
        }
      }
    }
  }
}
.card_box{
  width: 32.8%;
  // flex: 0 0 32.8%;
   border-radius: 1rem;
   overflow: hidden;
   position: relative;
   @media #{variables.$vpMobile600} {
    border-radius: 0.5rem;
    width: 100%;
  }
  .img-sec{
    height: 14.375rem;
    overflow: hidden;
    @media #{variables.$vpMobile600} {
      height: 10.25rem;
    }
    img{
      border-radius: 1rem 1rem 0 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.35s ease;
      @media #{variables.$vpMobile600} {
        border-radius: 0.5rem 0.5rem 0 0;
      }
    }
   
  }
  .tag{
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: 1;
    padding: 0.4rem .5rem 0.15rem;
    background-color: variables.$graySty6;
    border-radius: 0.5rem;
    @media #{variables.$vpMobile600} {
      top: 1rem;
      left: 1rem;
      border-radius: 0.25rem;
    }
    .tag--label{
      color: variables.$textStyleSecondary;
      text-transform: uppercase;
    }
  }
  .content_box{
    padding: 1.5rem;
    background-color: white;
    border-radius: 0 0 1rem 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    @media #{variables.$vpMobile600} {
      padding: 1rem;
      border-radius: 0 0 0.5rem 0.5rem;
      gap: 0.5rem;
    }
    .title{
      .card_main_title{
        color: variables.$blueSty1;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .decription_wrapper{
      width: 100%;
      .pera-sec{
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        .description{
          color: variables.$textStyleSecondary;
        }
      }
      .bottom_tagline{
       
        .card_meta{
          color: variables.$textStyleSecondary;
        }
      }
      .meta_wrapper{
        display: flex;
        .bottom_tagline{
          display: flex;
          gap: 3.3125rem;
          @media #{variables.$vpMobile600} {
            gap: 1rem;
          }
          .card_meta{
            color: variables.$textStyleSecondary;
            text-transform: uppercase;
          }
        }
        .action_btn{
          max-width: 100%;
          
          display: flex;
          flex-direction: row-reverse;
          gap: 0.5rem;
          justify-content: space-between;
          // padding-top: 1rem;
          @media #{variables.$vpMobile600} {
            padding-top: 0;
          }
          .file_ext{
            color: variables.$blueSty1;
          }
          .file_size{
            color: variables.$graySty1;
          }
        }
      }
    }
    .list_tag{
      width: max-content;
      padding: 0.25rem 0.5rem;
      background-color: #FBF8F7;
      border-radius: 0.5rem;
      @media #{variables.$vpMobile600} {
        border-radius: 0.25rem;
      }
      .tag--label{
        color: variables.$textStyleSecondary;
        text-transform: uppercase;
      }
    }
  }
  &.card_with_noImg{
  .content_box{
    display: flex;
    justify-content: space-between;
    height: 100%;
    flex-direction: column;
  }
}
  &.card_box--wide{
    max-width: 57.66%;
    flex: 0 0 57.66%;
    @media #{variables.$vpTablet1199} {
      max-width: 57.4%;
      flex: 0 0 57.4%;
    }
    @media #{variables.$vpTablet991} {
      max-width: 100%;
      flex: 0 0 100%;
    }
    .img-sec{
      height: 23.8125rem;
      @media #{variables.$vpMobile600} {
        height: 10.25rem;
      }
    }
    .content_box{
      background-color: variables.$graySty6;
    }
    .pera-sec{
      .description{
        color: variables.$textStyleSecondary;
      }
    }
  }
  &.card_with_noImg{
    .content_box{
      justify-content: space-between;
      @media #{variables.$vpMobile600} {
        min-height: 19.25rem;
      }
      .title{
        padding-top: 2.81rem;
      }
      .decription_wrapper{
        gap: 1rem;
        @media #{variables.$vpMobile600} {
          gap: 0.5rem;
        }
      }
    }
  }
  &:hover {
    .img-sec {
      img {
        transform: scale(1.05);
      }
    }
  }
}
.card_listing{
  max-width: 41.5%;
  @media #{variables.$vpTablet991} {
    max-width: 100%;
    flex: 0 0 100%;
  }
  .listing_card_box{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    gap: 1.5rem;
    background-color: variables.$graySty6;
    border-radius: 1rem;
    margin-top: 0.63rem;
    @media #{variables.$vpTablet991} {
      gap: 1rem;
    }
    @media #{variables.$vpMobile600} {
      border-radius: 0.5rem;
      padding: 0.5rem 1rem 0.5rem 0.5rem;
    }
    .img_wrap{
      max-width: 19.44%;
      flex: 0 0 19.44%;
      border-radius: 0.5rem;
      overflow: hidden;
      @media #{variables.$vpTablet991} {
        height: 5.875rem;
        max-width: 23.6%;
        flex: 0 0 23.6%;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.35s ease;
      }
    }
    .content_wrapper{
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      max-width: 100%;
      
      @media #{variables.$vpTablet991} {
        max-width: 100%;
        
      }
      .tag{
        padding: 0.4rem .5rem 0.15rem;
        background-color: white;
        border-radius: 0.5rem;
        width: max-content;
        .tag--label{
          color: variables.$textStyleSecondary;
          text-transform: uppercase;
        }
      }
      .title{
        .card_main_title{
          color: variables.$blueSty1;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .pera-sec{
        display: -webkit-box;       
        -webkit-line-clamp: 2;     
        -webkit-box-orient: vertical;
        overflow: hidden;      
        text-overflow: ellipsis; 
        .description{
          color: variables.$textStyleSecondary;
        }
      }
      .bottom_tagline{
        .card_meta{
          color: variables.$textStyleSecondary;
          text-transform: uppercase;
          
        }
      }
    }
    &:first-child{
      margin-top: 0;
    }
    &:hover {
      .img_wrap {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
}
.pressRelease--cards {
  &.card_listing {
    .listing_card_box {
      .content_wrapper {
        .title {
          .card_main_title {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
    }
  }
}
.list-view-listing {
  .card_with_noImg{
    .content_box{
      width: 100% !important;
    }
  }
}