@use "../global/variables.scss";

.invest-sec {
  position: relative;
  .box {
    .invest-wrapper {
      height: 100vh;
      display: flex;
      justify-content: space-between;
      position: relative;
      margin-top: 8rem !important;
      transition: height 0.3s ease;
      @media #{variables.$vpTablet1199} {
        flex-direction: column;
        align-items: center;
        justify-content: start;
        margin-top: 3rem !important;
        height: auto;
      }
      
      .leftBar-content {
        width: 24.1%;
        display: flex;
        align-items: flex-start;
        height: 100%;
        transition: padding-top 0.3s ease-in-out;
        @media #{variables.$vpTablet1199} {
          width: 100%;
          height: auto;
          padding-bottom: 2rem;
        }
        
       
        .content-wrap {
          .body1 {
            padding-bottom: 1.5rem;
            color: #0d2640;
           
            &:first-child {
              margin-bottom: 0;
            }
            @media #{variables.$vpTablet1199} {
              margin-bottom: 1rem;
            }
            @media #{variables.$vpMobile600} {
              margin-bottom: 0;
            }
          }
          .primary_btn {
            margin-top: 1.5rem;
          }
          .desktop-btn {
            @media #{variables.$vpTablet1199} {
              display: none;
            }
          }
        }
      }
      .animation-overlay {
        text-align: center;
        position: relative;
        width: 32rem;
        height: 32rem;
        display: flex;
        justify-content: center;
        align-items: center;
       
        @media #{variables.$vpTablet1199} {
          flex-direction: column;
          width: 30rem !important;
          height: 30rem !important;
          flex: 0 0 30rem !important;
        }
        
       @media screen and (max-width: 768px) {
          width: 35rem !important;
          height: 35rem !important;
          flex: 0 0 35rem !important;
       }

        @media #{variables.$vpMobile600} {
          width: 18.375rem !important;
          height: 18.375rem !important;
          flex: 0 0 18.375rem !important;
          margin: 0;
        }
        svg {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          transform: scaleX(-1);
          transform-origin: center center;
          path {
            stroke-dasharray: 1756;
            stroke-dashoffset: 1756;
            stroke: variables.$sunriseGold;
            stroke-width: 2;
            fill: none;
            width: 100%;
            height: 100%;
          }
        }
        .title {
          // font-size: 130px;
          font-weight: 400;
          z-index: 1;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
          max-width: 29rem;
          font-size: 6.875rem;
          @media #{variables.$vpTablet1199} {
            font-size: 6rem;
            max-width: 25rem;
          }
          @media #{variables.$vpMobile600} {
            max-width: 17rem;
            font-size: 4rem;
          }
        }
        .mob-btn {
          display: none;
          @media #{variables.$vpTablet1199} {
            display: block;
            margin-top: 1rem;
            position: relative;
            z-index: 9;
            .btn {
              padding: 0 1rem;
            }
          }
        }
        .bullet-points {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          // pointer-events: none;
          transition: 0.3s ease-in-out;
          pointer-events: auto;
          overflow: visible;
          .bullet {
            // width: 20px;
            // height: 20px;
            width: 1.125rem;
            height: 1.0625rem;
            transition: 0.3s ease-in-out;
            cursor: pointer;

            svg {
              width: 100%;
              height: 100%;
              transform: none;
              transition: 0.3s ease-in-out;
              path {
                fill: variables.$sunriseGold;
              }
            }
            @media #{variables.$vpMobile600} {
              width: 0.5625rem;
              height: 0.5625rem;
            }
            &.active {
              width: 3.3125rem;
              height: 3.125rem;
              padding: 4.65rem 4.5rem;
              border: 0.06rem solid variables.$sunriseGold;
              border-radius: 100%;
              transition: 0.3s ease-in-out;
              svg {
                padding: 3.2rem 2rem;
                transition: 0.3s ease-in-out;
                @media #{variables.$vpTablet1199} {
                  padding: 2rem;
                }
                @media #{variables.$vpMobile600} {
                  padding: 1.3rem;
                }
              }
              @media #{variables.$vpTablet1199} {
                padding: 2.9rem;
              }
              @media #{variables.$vpMobile600} {
                width: 1.02569rem;
                height: 0.96763rem;
                padding: 1.813rem;
                border-width: 0.02rem;
              }
            }
          }
        }
      }
      .rightBar-content {
        width: 23%;
        // flex: 0 0 30%;
        display: flex;
        align-items: flex-end;
        height: 100%;
        padding-bottom: 13rem;
        .wrap {
          .content-wrap {
            @media #{variables.$vpMobile600} {
              padding: 0 2rem;
            }
            .bottom_title {
              color: variables.$blueSty1;
            }
          }
        }

        @media #{variables.$vpTablet1199} {
          width: 100%;
          height: auto;
          padding-bottom: 3rem;
          justify-content: center;
          text-align: center;
          padding-top: 2rem;
        }
      }
    }
  }
}

.rightBar-content .active {
  transition: opacity 0.8s ease;
}

.rightBar-content > div:not(.active) {
  transition: opacity 0.8s ease;
}
