@use "../global/variables.scss";

.sec-header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    transition: all 0.5s ease;
    transform: translateY(-50rem);
    @media #{variables.$vpTablet1199} {
        overflow: hidden;
    }
    .header-wrapper {
        display: flex;
        justify-content: space-between;
        padding: 1.5rem 0;
        .postBox {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            .logo-box {
                img,svg {
                    width: 10.5625rem;
                    height: 5.6875rem;
                }
            }
            .menu-wrapper {
                display: flex;
                align-items: center;
            }
            .menu-box {
                padding-right: 1rem;
                position: relative;
                &:after {
                    content: "";
                    position: absolute;
                    width: 1px;
                    height: calc(100% - 2rem);
                    background: rgba(variables.$white, $alpha: 0.5);
                    top: 50%;
                    right: 0;
                    transform: translateY(-50%);
                }
                ul {
                    display: flex;
                    li {
                        padding: 1rem;
                        position: relative;
                        display: flex;
                        align-items: center;
                        .item--wrapper {
                            display: flex;
                            align-items: center;
                        }
                        a {
                            text-transform: uppercase;
                            letter-spacing: 0.04rem;
                        }
                        svg {
                            width: 1rem;
                            height: 1rem;
                            margin-left: 0.37rem;
                            margin-top: -0.1rem;
                            transition: transform 0.5s ease;
                        }
                        .subMenuBox {
                            position: absolute;
                            padding: 1.5rem;
                            border-radius: 0.75rem;
                            background: variables.$graySty6;
                            backdrop-filter: blur(12px);
                            min-width: 17.75rem;
                            top: 3.3rem;
                            left: 0;
                            opacity: 0;
                            visibility: hidden;
                            transform: translateY(-1rem);
                            overflow: hidden;
                            
                            ul {
                                display: flex;
                                flex-direction: column;
                                li {
                                    padding: 0.62rem 0;
                                    border-bottom: 0.5px solid rgba(variables.$sunriseGold, $alpha: 0.2);
                                    // transform: translateX(-50px);
                                    opacity: 0;
                                    &:first-child {
                                        padding-top: 0;
                                    }
                                    &:last-child {
                                        padding-bottom: 0;
                                        border-bottom: 0;
                                    }
                                    a {
                                        color: #00121C;
                                        width: max-content;
                                        display: block;
                                        transition: all 0.5s ease;
                                        line-height: 1;
                                        &:hover {
                                            color: variables.$sunsetGoldSty2;
                                            transform: translateX(0.35rem);
                                        }
                                    }
                                }
                            }
                        }
                        &.show-menu {
                            svg {
                                transform: rotate(-180deg);
                            }
                            .subMenuBox {
                                visibility: visible;
                                opacity: 1;
                                transform: translateY(0);
                                transition: all 0.5s ease;
                                ul {
                                    li {
                                        transition: all 1s ease;
                                        transform: translateX(0);
                                        // transition-delay: 0.1s;
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .menu-secondary {
                padding-left: 1rem;
                display: flex;
                align-items: center;
                .lang--switch {
                    padding: 0 1rem;
                    a {
                        text-transform: uppercase;
                        letter-spacing: 0.04rem;
                    }
                    &:last-child {
                        padding-right: 0;
                    }

                }
                .header-btn {
                    .btn {
                        height: 2.5rem;
                    }
                }
                .getInTouch {
                    padding: 0.69rem 1.5rem;
                    border-radius: 9.375rem;
                    border: 0.75px solid rgba(255, 255, 255, 0.15);
                    background: rgba(199, 152, 107, 0.10);
                    a {
                        text-transform: uppercase;
                        letter-spacing: 0.04rem;
                    }
                }
                .hamburger--wrapper {
                    display: none;
                }
            }
        }
    }
    @media #{variables.$vpTablet1199} {
        .header-wrapper {
            .postBox {
                .logo-box {
                    svg {
                        width: 10.5rem;
                        height: auto;   
                    }
                }
                .menu-box {
                    opacity: 0;
                    visibility: hidden;
                    height: 0;
                    width: 0;
                    ul {
                        li {
                            flex-direction: column;
                            align-items: flex-start;
                            padding: 1.5rem 0;
                            border-bottom:0.5px solid variables.$white ;
                            opacity: 0;
                            visibility: hidden;
                            transform: translateY(1rem);
                            .item--wrapper {
                                width: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                            }
                            .subMenuBox {
                                position: relative;
                                height: 0;
                                opacity: 0;
                                width: 0;
                                visibility: hidden;
                                padding: 0;
                                background: transparent;
                                backdrop-filter: none;
                                padding: 0;
                                transform: translateY(0);
                                ul {
                                    padding: 2rem 0 0.5rem;
                                    li {
                                        padding: 1rem 0;
                                        border-bottom: 0;
                                        a {
                                            color: variables.$white;
                                        }
                                    }
                                }
                            }
                            &.show-menu {
                                .subMenuBox {
                                    width: 100%;
                                    height: max-content;
                                    opacity: 1;
                                    visibility: visible;
                                    top: auto;
                                }
                            }
                        }
                    }
                }
                .menu-secondary {
                    .header-btn {
                        display: none;
                    }
                    .hamburger--wrapper {
                        display: block;
                        width: 1.5625rem;
                        height: 1rem;
                        position: relative;
                        margin-left: 1.5rem;
                        transition: margin 0.5s ease;
                        span {
                            position: absolute;
                            width: 100%;
                            height: 0.04rem;
                            top: 0;
                            background-color: aliceblue;
                            transition: all 0.5s ease;
                            &:nth-child(2) {
                                top: 0.4rem;
                            }
                            &:nth-child(3) {
                                top: 0.8rem;
                            }
                        }
                    }
                }            
            }
        } 
    }
}
@media #{variables.$vpMobile600} {
    .sec-header {
        .header-wrapper {
            .postBox {
                .logo-box {
                    svg {
                        width: 6.5rem;
                        height: 3.5rem;
                    }
                }
                .menu-secondary {
                    .hamburger--wrapper {
                        margin-left: 0.5rem;
                    }
                }
                .menu-box {
                    ul {
                        li {
                            .subMenuBox {
                                ul {
                                    li {
                                        a {
                                            font-size: 0.875rem;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
.menu--footer {
    display: none;
    position: relative;
}
body {
    &:after {
        content: "";
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100vh;
        background: linear-gradient(0deg, #9E7140 19.58%, #CCA374 91.99%), #000;
        transform: translateX(100rem);
        transition: transform 0.6s ease;
        z-index: 2;
    }
    &.menu--open {
        &:after {
            transform: translateX(0);
        }
        .sec-header {
            background: transparent !important;
            overflow: visible;
            .header-wrapper {
                .postBox {
                    position: relative;
                    .logo-box {
                        svg {
                            path {
                                fill: variables.$white !important;
                            }
                        }
                    }
                    .menu-box {
                        opacity: 1;
                        visibility: visible;
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        left: 0;
                        top: 7rem;
                        padding-right: 0;
                        min-height: calc(100vh - 12rem);
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        &:after {
                            display: none;
                        }
                        ul {
                            flex-direction: column;
                            overflow-y: scroll;
                            height: 60vh;
                            padding-right: 0.5rem;
                            li {
                                transition: all 0.5s ease;
                                transition-delay: 1s;
                                opacity: 1;
                                visibility: visible;
                                transform: translateY(0);
                                justify-content: space-between;
                                &:nth-child(2) {
                                    transition-delay: 1.2s;
                                }
                                &:nth-child(3) {
                                    transition-delay: 1.4s;
                                }
                                &:nth-child(4) {
                                    transition-delay: 1.6s;
                                }
                                &:nth-child(5) {
                                    transition-delay: 1.8s;
                                }
                                &:nth-child(6) {
                                    transition-delay: 2s;
                                }
                                &:nth-child(7) {
                                    transition-delay: 2.2s;
                                }
                                &:nth-child(8) {
                                    transition-delay: 2.4s;
                                }
                                &:nth-child(9) {
                                    transition-delay: 2.6;
                                }
                                &:nth-child(10) {
                                    transition-delay: 2.8s;
                                }
                                a {
                                    font-size: 1.5rem;
                                    @media #{variables.$vpMobile600} {
                                        font-size: 1.25rem;
                                    }
                                }
                                ul {
                                    overflow-y: hidden;
                                     height: auto;
                                }
                            }
                        }
                    }
                }
                .menu-secondary {
                    .lang--switch {
                        a {
                            color: variables.$white !important; 
                        }
                    }
                    .hamburger--wrapper {
                        margin-left: 1.5rem;
                        span {
                            transform: rotate(-45deg);
                            top: 0.45rem;
                            width: 70%;
                            left: -0.55rem;
                            background-color: variables.$white !important; 
                            &:nth-child(2) {
                                top: 0.45rem;
                                transform: rotate(45deg);
                            } 
                            &:nth-child(3) {
                                display: none;
                            } 
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 3rem;
                            height: 3rem;
                            border-radius: 50%;
                            border: 1px solid #fff;
                            top: 50%;
                            left: 0;
                            transform: translate(-50%, -50%);
                        }
                    }
                }
            }
        }
        .menu--footer {
            display: block;
            bottom: 2rem;
            width: 100%;
            transition-delay: 2s;
            .sec-btn {
                a {
                    width: 100%;
                    justify-content: center;
                }
            }
        }
    }
    &.bodyScrolled {
        .sec-header {
            position: fixed;
            top: 0;
            opacity: 0 !important;
            z-index: 0;
        }
    }
    &.scrollTop {
        .sec-header {
            position: fixed;
            top: 0;
            background: variables.$graySty6;
            z-index: 10;
            .header-wrapper {
                padding: 0.75rem 0;
                @media #{variables.$vpMobile600} {
                    padding: 0.625rem 0;
                }
                .postBox {
                    .logo-box {
                        svg {
                            width: 6.96431rem;
                            height: 3.75rem;
                            path {
                                fill: variables.$blueSty1;
                            }
                        }
                    }
                    .menu-item {
                       .cta-txt {
                        color: variables.$blueSty1;
                        @media #{variables.$vpTablet1199} {
                            color: variables.$white;
                            font-weight: 400;
                        }
                       } 
                       svg {
                        path {
                            stroke: variables.$blueSty1;  
                            @media #{variables.$vpTablet1199} {
                                stroke: variables.$white;
                            }
                        }
                       }
                    }
                    .menu-box ul li .subMenuBox {
                        top: 4.4rem;
                        border-radius: 0 0 0.75rem 0.75rem;
                        @media #{variables.$vpTablet1199} {
                            top: 0;
                        }
                    }
                }
                .menu-secondary {
                    .header-btn {
                        .btn-dark {
                            color: variables.$blueSty1;
                        } 
                    }
                    .hamburger--wrapper {
                        span {
                           background-color: variables.$blueSty1; 
                        }
                    }
                    .lang--switch {
                        a {
                            color: variables.$blueSty1;
                        }
                    }
                }
            }
        }
    }
}
.menu-item {
    &:hover {
        .cta-txt {
            color: #AA865E !important;
        }
        svg {
            path {
                stroke: #AA865E !important;
            }
        }
        @media #{variables.$vpTablet1199} {
            .cta-txt {
                color: #ffffff;
            }
            svg {
                path {
                    stroke: #ffffff;
                }
            }
        }
    }
}