@use "../global/variables.scss";
.board-of-director-sec {
  z-index: 1;
  display: block;
  width: 100%;
  padding: 0;
  position: relative;
  .heading-title {
    color: variables.$blueSty1;
    margin-bottom: 3rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
  }
  .leadership-wrapper {
    display: flex;
    justify-content: space-between;
    border-radius: 12px;
    overflow: hidden;
    min-height: 23.125rem;
    position: relative;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
    }
    .content-wrapper {
      max-width: 67.5%;
      flex: 0 0 67.5%;
      padding: 3.048rem;
      background: white;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        padding: 2rem 1.25rem;
      }
      .text-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        max-width: 32rem;
        .sub-heading {
          color: variables.$textStyleSecondary;
        }
        .main-heading {
          margin-block: 1rem;
          color: variables.$blueSty1;
        }
        .main-designation {
          color: variables.$textStyleSecondary;
        }
      }
    }
    .img-box {
      flex: 0 0 32.5%;
      max-width: 32.5%;
      position: relative;
      padding-top: 29%;
      height: 23.09525rem;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        padding-top: 87.1%;
      }
      picture {
        height: 100%;
        top: 0;
        position: absolute;
        width: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    .leadership-art {
      position: absolute;
      right: 1.25rem;
      top: 2.5rem;
      width: 15rem;
      height: 16.75rem;
      svg {
        width: 15rem;
        height: 16.75rem;
      }
    }
  }
  .member-card-wrapper {
    display: flex;
    flex-wrap: wrap;
    column-gap: 16px;
    row-gap: 16px;
    margin-top: 1rem;
    @media #{variables.$vpMobile600} {
      row-gap: 8px;
      margin-top: 0.5rem;
    }
    .member-card {
      flex: 0 0 49.2%;
      max-width: 49.2%;
      background: white;
      padding: 1.143rem 2.143rem;
      border-radius: 8px;
      overflow: hidden;
      display: flex;
      align-items: center;
      column-gap: 1.594rem;
      @media #{variables.$vpTablet1199} {
        flex: 0 0 49%;
        max-width: 49%;
      }
      @media #{variables.$vpTablet991} {
        flex: 0 0 48.5%;
        max-width: 48.5%;
      }
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        padding: 0.875rem;
      }
      .member-details {
        display: flex;
        flex-direction: column;
        .member-title {
          color: black;
        }
        .designation {
          color: variables.$graySty1;
          margin-top: 0.5rem;
          @media #{variables.$vpMobile600} {
            margin-top: 0.25rem;
          }
        }
      }
      .member-img {
        width: 7.25rem;
        height: 7.25rem;
        flex: 0 0 7.25rem;
        max-width: 7.25rem;
        border-radius: 50%;
        border: 2px solid variables.$sunriseGold;
        overflow: hidden;
        position: relative;
        @media #{variables.$vpMobile600} {
          width: 4.188rem;
          height: 4.188rem;
          flex: 0 0 4.188rem;
          max-width: 4.188rem;
        }
        picture {
          height: 100%;
          top: 0;
          position: absolute;
          width: 100%;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }
  .managment-wrapper {
    display: flex;
    justify-content: space-between;
    border-radius: 12px;
    overflow: hidden;
    min-height: 17.375rem;
    position: relative;
    padding: 2rem;
    background: white;
    border-radius: 12px;
    overflow: hidden;
    column-gap: 2.5rem;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
      align-items: center;
      padding: 1.25rem 2rem;
    }
    .content-wrapper {
      max-width: 79%;
      flex: 0 0 79%;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        padding-top: 2rem;
        text-align: center;
        .desktop-btn {
          margin: auto;
        }
      }
      .text-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        max-width: 32rem;
        .main-heading {
          color: variables.$blueSty1;
        }
        .main-designation {
          color: variables.$textStyleSecondary;
          margin-block-start: 1rem;
          margin-block-end: 1.5rem;
          @media #{variables.$vpMobile600} {
            margin-block-end: 2rem;
          }
        }
      }
    }
    .img-box {
      flex: 0 0 13.375rem;
      max-width: 13.375rem;
      height: 13.375rem;
      width: 100%;
      padding-top: 13.375rem;
      position: relative;
      border: 3px solid variables.$sunriseGold;
      border-radius: 50%;
      overflow: hidden;
      @media #{variables.$vpMobile600} {
        flex: 0 0 13.375rem;
        max-width: 13.375rem;
      }
      picture {
        height: 100%;
        top: 0;
        position: absolute;
        width: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    .leadership-art {
      position: absolute;
      right: 4.5rem;
      top: 0.5rem;
      width: 15rem;
      height: 16.75rem;
      svg {
        width: 15rem;
        height: 16.75rem;
      }
      @media #{variables.$vpMobile600} {
        display: none;
      }
    }
  }
  .horizontal-line {
    background-color: variables.$sunriseGold;
    svg {
      path {
        fill: variables.$sunriseGold;
      }
    }
  }
  &.sty-02 {
    .leadership-wrapper {
      .content-wrapper {
        .sec-btn {
          .btn_Icon {
            background: rgba(199, 152, 107, 0.1);
            svg {
              path {
                transition: all 0.5s ease;
              }
            }
            &:hover {
              svg {
                path {
                  stroke: white;
                }
              }
              @media #{variables.$vpTablet1199} {
                svg {
                  path {
                    stroke: variables.$blueSty1;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
/* Modal backdrop */
#leader-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Dim background */
  display: flex; /* Hidden initially */
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0; /* Initially hidden */
  visibility: hidden;
  transition: all 0.3s ease-out; /* Smooth fade-in/fade-out effect */

  &.show {
    display: flex;
    opacity: 1; /* Fade in */
    visibility: visible;
  }
}

#form-modal-id {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Dim background */
  display: flex; /* Hidden initially */
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0; /* Initially hidden */
  visibility: hidden;
  transition: all 0.3s ease-out; /* Smooth fade-in/fade-out effect */

  &.show {
    display: flex;
    opacity: 1; /* Fade in */
    visibility: visible;
  }
}

/* Modal content */
.modal-content {
  background-color: white;
  padding: 20px;
  width: 37.625rem;
  max-width: 100%;
  height: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: absolute;
  right: 0;
  transform: translateX(100%);
  transition: transform 0.5s ease-out;
  padding: 4.5rem 2.5rem;
  @media #{variables.$vpMobile600} {
    width: 100%;
    padding: 3.625rem 1.25rem;
  }
  .main-content {
    display: flex;
    align-items: center;
    column-gap: 1.25rem;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 1.5rem;
    }

    .modal-member-img {
      width: 8.438rem;
      height: 8.438rem;
      flex: 0 0 8.438rem;
      max-width: 8.438rem;
      border-radius: 50%;
      overflow: hidden;
      border: 3px solid variables.$sunriseGold;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .headings {
      .modal-title {
        color: black;
        margin-bottom: 0.5rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 0.25rem;
        }
      }

      .modal-designation {
        color: variables.$graySty1;
      }
    }
    .modal-verctor-top {
      position: absolute;
      top: 0;
      left: 0;
      /*rtl:raw:transform: scaleX(-1);*/
    }
    .modal-verctor-bottom {
      position: absolute;
      bottom: 0;
      right: 0;
      /*rtl:raw:transform: scaleX(-1);*/
    }
  }
  .modal-description {
    overflow: auto;
    max-height: 75%;
    color: black;
    margin-top: 4rem;
    scrollbar-width: none;
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
      display: none;
    }
    @media #{variables.$vpMobile600} {
      margin-top: 2.5rem;
    }

    p + p {
      margin-top: 1.5rem;
    }

    /* Hide scrollbar for WebKit browsers (Chrome, Safari, Edge) */
    &::-webkit-scrollbar {
      display: none;
    }
  }

  &.form-modal {
    width: 44.125rem;
    padding: 4.25rem 2.5rem 3.5rem !important;

    @media #{variables.$vpMobile600} {
      padding: 4.25rem 1.25rem 2.5rem 1.25rem !important;
    }

    .customScroll_bar {
      height: calc(100vh - 6.5rem);

      .title {
        margin-bottom: 1.5rem;
        color: variables.$blueSty1;

        z-index: 1;
        position: relative;
        max-width: 30.625rem;
        &.sty1 {
          color: variables.$blueSty1;
        }
        @media #{variables.$vpMobile600} {
          margin-bottom: 1rem;
          max-width: 100%;
        }
        span {
          color: variables.$sunsetGoldSty2;
        }
      }
      .description {
        max-width: 30.625rem;
        @media #{variables.$vpMobile600} {
          max-width: 100%;
        }
      }
    }
  }
}

.modal.show .modal-content {
  transform: translateX(0);
}

.modal.hide .modal-content {
  transform: translateX(100%);
  /*rtl:raw:transform: translateX(-100%);*/
}

/* Close button */
.close {
  position: absolute;
  left: -4.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 1;
  @media #{variables.$vpMobile600} {
    right: 1.25rem;
    top: 2.75rem;
    left: unset;
    border: 0.643px solid rgba(0, 0, 0, 0.15);
  }

  svg {
    width: 1.5rem;
    height: 1.5rem;
  }
  &:hover {
    background: variables.$sunsetGold;
    @media #{variables.$vpTablet1199} {
      background: white;
    }
    svg {
      path {
        stroke: white;
        @media #{variables.$vpTablet1199} {
          stroke: variables.$blueSty1;
        }
      }
    }
  }
}
