@use "../global/variables.scss";

.map-wrapper {
  z-index: 1;
  position: relative;
  .map-sec {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    column-gap: 7.75rem;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
      row-gap: 2.5rem;
    }
    .contact-sec {
      flex: 0 0 30%;
      max-width: 30%;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        width: 100%;
      }
      .title {
        color: white;
      }
      .para {
        color: white;
        margin-top: 1rem;
        margin-bottom: 2rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.5rem;
        }
      }
      .details-sec {
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 1rem;
        padding: 1.5rem 0;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          background-color: rgba(variables.$white, 0.15);;
        }
        &:first-child {
          padding-top: 0;
        }
        &:last-child {
          padding-bottom: 0;
          &::after {
            display: none;
          }
        }
        .contact-icon {
          width: 3rem;
          height: 3rem;
          border-radius: 50%;
          overflow: hidden;
          border: 1px solid rgba(255, 255, 255, 0.15);
          background: rgba(199, 152, 107, 0.1);
          position: relative;
          .icon {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 1.5rem;
            height: 1.5rem;
            transform: translate(-50%, -50%);
          }
        }

        .txt-box {
          .txt {
            color: white;
          }
        }
      }
    }
    .map-box {
      flex: 0 0 58.2%;
      max-width: 58.2%;
      height: 28.563rem;
      background: #ccc;
      border-radius: 1rem;
      overflow: hidden;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
        width: 100%;
        height: 18.688rem;
      }
    }
  }
}
