@use "../global/variables.scss";

.who-we-are-section-dark {
  padding-top: 6.688rem;
  // position: relative;
  @media #{variables.$vpMobile600} {
    padding-top: 2.25rem;
  }
  .wrapper {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    overflow: hidden;
    z-index: 1;
    overflow: visible;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
    }

    .content {
      flex: 0 0 40.8%;
      max-width: 40.8%;
      overflow: visible;
      @media #{variables.$vpMobile600} {
        flex: unset;
        max-width: 100%;
      }
      .heading3 {
        color: white;
      }

      .body1 {
        max-width: 25.81rem;
        padding-top: 2rem;
        padding-bottom: 2rem;
        color: white;
        @media #{variables.$vpMobile600} {
          max-width: 100%;
          padding-top: 1.5rem;
          padding-bottom: 1.5rem;
        }
      }
    }

    .image-container {
      // width: 39.375rem;
      height: 29.625rem;
      flex: 0 0 49.22%;
      max-width: 49.22%;
      border-radius: 0.75rem;
      overflow: hidden;
      @media #{variables.$vpMobile600} {
        flex: unset;
        max-width: 100%;
        margin-top: 0.5rem;
        width: 21.875rem;
        height: 16.4375rem;
        border-radius: 0.5rem;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &.sty1 {
    .wrapper {
      .sec-content {
        margin-top: 2rem;
        p {
          padding-top: 0;
          padding-bottom: 1rem;
        }
      }
    }
  }

  &.sty2 {
    padding-top: 4.375rem;
    @media #{variables.$vpMobile600} {
      padding-top: 0;
    }
    .content {
      .body1 {
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          padding-top: 0.5rem;
        }
      }
    }
  }
  .vertical-line-wrapper {
    position: relative;
    margin-bottom: 8.063rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 7.063rem;
    }
    .vertical-line-2,
    .vertical-line-3 {
      width: 1px;
      height: 0%;
      background-color: white;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      z-index: 2;

      .icon-1 {
        position: absolute;
        left: -0.25rem;
        top: -2px;
        will-change: transform;
        transform: translateZ(0);
      }
      .icon-2 {
        position: absolute;
        left: -0.25rem;
        top: 10%;
        will-change: transform;
        transform: translateZ(0);
      }
      .icon-3 {
        position: absolute;
        left: -0.25rem;
        bottom: -2px;
        will-change: transform;
        transform: translateZ(0);
      }
    }
  }
}
