@use "../global/variables.scss";

.who-we-are-section-rev {
  .wrapper {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: space-between;
    overflow: hidden;
    z-index: 1;
    overflow: visible;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
    }

    .content {
      flex: 0 0 40.8%;
      max-width: 40.8%;
      overflow: visible;
      @media #{variables.$vpMobile600} {
        flex: unset;
        max-width: 100%;
      }
      .heading3{
        color: variables.$blueSty1;
      }

      .body1 {
        max-width: 25.81rem;
        padding-top: 2rem;
        padding-bottom: 0;
        color: variables.$blueSty1;
       
        @media #{variables.$vpMobile600} {
          max-width: 100%;
          padding-top: 1.5rem;
          padding-bottom: 0;
        }
      }
    }

    .image-container {
      // width: 39.375rem;
      height: 29.625rem;
      flex: 0 0 49.22%;
      max-width: 49.22%;
      border-radius: 0.75rem;
      overflow: hidden;
      @media #{variables.$vpMobile600} {
        flex: unset;
        max-width: 100%;
        margin-top: 2.38rem;
        width: 21.875rem;
        height: 16.4375rem;
        border-radius: 0.5rem;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &.sty1 {
    .wrapper {
      .sec-content {
        margin-top: 2rem;
        p {
          padding-top: 0;
          padding-bottom: 1rem;
        }
      }
    }
  }
}
