@use "../global/variables.scss";

#section3 {
  // background-color: #0D2640;
  // background: linear-gradient(0deg, #061930 -2.04%, #0F3A5B 100%), linear-gradient(123deg, #061930 30.48%, #0F3A5B 101.87%);
  color: white;
  padding-top: 12.06rem;
  padding-bottom: 6.69rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  z-index: 1;
  @media #{variables.$vpMobile600} {
    flex-direction: column-reverse;
    padding-top: 4rem;
    padding-bottom: 4.37rem;
  }

  .card-container {
    // display: flex;
    // flex-direction: row;
    // gap: 20px;
    // position: relative;
    // padding: 0;
    // margin: 0;
    // will-change: transform;
    // transform: translateZ(0);
    .card {
      margin-right: 0.62rem;
      background: #fff;
      color: black;
      border: 1px solid #ccc;
      padding: 20px;
      // width: 100%;
      width: 19.18rem;
      cursor: pointer;
      will-change: transform;
      transform: translateZ(0);
      // text-align: center;
      // user-select: none;
      // flex-shrink: 0;
    }
  }

  .content-wrapper {
    min-width: 100%;
    padding-bottom: 2.51rem;
    @media #{variables.$vpMobile600} {
      padding-top: 0;
    }
    .promise-description {
      padding-top: 1.75rem;
      max-width: 26.56rem;
      @media #{variables.$vpMobile600} {
        padding-top: 1rem;
      }
    }
  }

  .promise {
    margin-top: 2.5rem;

    &.wrapper {
      margin-left: 5rem;
      margin-right: 5rem;
      width: 100%;
      @media #{variables.$vpTablet1199} {
        margin-left: 2.5rem;
        margin-right: 2.5rem;
      }
      @media #{variables.$vpMobile600} {
        margin-left: 1.25rem;
        margin-right: 1.25rem;
      }
    }

    .text-card {
      // margin-inline: 0.313rem;
      height: 19.438rem;
      // width: 15.5rem;
      flex: 0 0 16.6%;
      max-width: 16.6%;
      padding: 1rem;
      flex-shrink: 0;
      border-radius: 12px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border: 0.762px solid rgba(255, 255, 255, 0.4);
      background: rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(4.571428298950195px);
      overflow: hidden;
      will-change: transform;
      // transition: all 0.4s ease;

      @media #{variables.$vpTablet1199} {
        flex: 0 0 22%;
        max-width: 22%;
      }
      @media #{variables.$vpMobile600} {
        flex: 0 0 60%;
        max-width: 60%;
      }

      .icon {
        position: relative;
        width: 2.5rem;
        height: 2.5rem;

        svg {
          width: 2.5rem;
          height: 2.5rem;
          display: block;

          .center {
            opacity: 0;
            transition: opacity 0.15s ease;
          }

          .border {
            stroke: variables.$white;
            transition: stroke 0.15s ease;
          }
        }
      }

      .text-sec {
        position: relative;
        .heading {
          // transform: translateY(0);
          transition: transform 0.15s ease, margin 0.15s ease;
          color: variables.$blueSty1;
          // @media #{variables.$vpMobile600} {
          //   transform: translateY(0);
          //   margin-bottom: 1rem;
          // }
        }
        .para {
          opacity: 1;
          // transform: translateY(0);
          overflow: hidden;
          color: variables.$white;
          transition: opacity 0.15s ease, transform 0.15s ease,
            max-height 1s ease;
          @media #{variables.$vpMobile600} {
            opacity: 1;
            // transform: translateY(0);
            // max-height: 1rem;
            // min-height: 1rem;
          }
        }
      }

      &:hover {
        background: white;
        svg {
          .center {
            opacity: 1;
          }

          .border {
            stroke: variables.$sunsetGoldSty2;
          }
        }

        .text-sec {
          .heading {
            // transform: translateY(0);
            margin-bottom: 1rem;
            color: variables.$sunsetGoldSty2;
          }
          .para {
            opacity: 1;
            // transform: translateY(0);
            // max-height: 6rem;
            color: variables.$blueSty1;
          }
        }
      }
    }

    /* Marquee Base */
    .marquee {
      display: flex;
      overflow: hidden;
      gap: 10px;
      padding-bottom: 0.1rem;
      &:hover {
        .marquee__item {
          animation-play-state: paused;
        }
      }
    }
    .marquee--overflow-visible {
      overflow: visible;
    }
    .marquee--paused .marquee__item {
      animation-play-state: paused;
    }

    /* Marquee animation */
    .marquee__item {
      --marquee-item-position: calc(var(--marquee-item-index) + 1);
      --duration: calc(var(--marquee-speed) * 1s);
      --duration-per-item: calc(var(--duration) / var(--marquee-count));
      --delay: calc(
        -1 * var(--duration-per-item) * (var(--marquee-count) -
              var(--marquee-item-position))
      );
    }

    .marquee-animate {
      animation: marquee var(--duration) var(--delay) linear infinite;
      /*rtl:raw:animation: marquee-rtl var(--duration) var(--delay) linear infinite;*/
    }

    @keyframes marquee-rtl {
      from {
        translate: calc(
          -1 * (100% + 10px) * (var(--marquee-count) -
                var(--marquee-item-position))
        );
      }
      to {
        translate: calc((100% + 10px) * var(--marquee-item-position));
      }
    }
    @keyframes marquee {
      from {
        translate: calc(
          (100% + 10px) * (var(--marquee-count) - var(--marquee-item-position))
        );
      }
      to {
        translate: calc(-1 * (100% + 10px) * var(--marquee-item-position));
      }
    }
  }
}
