@use "../global/variables.scss";

.inner-banner-wrapper {
  position: relative;
  height: 100vh;
  // max-height: 40rem;
  width: 100%;
  @media #{variables.$vpMobile600} {
    height: 31.25rem;
  }
  .img--wrapper {
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  #section1 {
    color: white;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    background: 
    linear-gradient(180deg, rgba(158, 113, 64, 0) 50%, #9E7140 100%), 
    linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);

    .text-container {
      white-space: normal;
      overflow: hidden;
      width: 100vw;
      position: relative;
      will-change: transform;
      transform: translateZ(0);
      margin-bottom: 2.31rem;
      max-width: 57rem;
      @media #{variables.$vpMobile600} {
        margin-bottom: 2rem;
        max-width: 100%;
      }
    }

    .bottom-wrapper {
      position: relative;

      .bottom-content {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        padding-top: 2.13rem;
        padding-bottom: 3.75rem;
        min-height: 11.9rem;

        @media #{variables.$vpMobile600} {
          min-height: 5.59rem;
        }

        .description {
          // max-width: 30.4375rem;

          max-width: 50%;
          min-height: 3.75rem;
          flex: 0 0 50%;
          padding-left: 2.81rem;
          padding-right: 6.75rem;
          @media #{variables.$vpTablet1199} {
            padding-right: 0;
          }
          @media #{variables.$vpMobile600} {
            display: none;
          }
        }
      }
    }

    .description-moblie {
      display: none;
      @media #{variables.$vpMobile600} {
        padding-bottom: 1.54rem;
        display: block;
      }
    }
  }

  .inner-banner-vertical-line {
    width: 1px;
    height: 0%;
    background-color: white;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: 2;

    .icon-1 {
      position: absolute;
      left: -0.28rem;
      top: 14%;
      will-change: transform;
      transform: translateZ(0);
    }
    .icon-2 {
      position: absolute;
      left: -0.28rem;
      bottom: 10%;
      will-change: transform;
      transform: translateZ(0);
    }
    .icon-3 {
      position: absolute;
      left: -0.28rem;
      bottom: -2px;
      will-change: transform;
      transform: translateZ(0);
    }
  }
  &.detail-inner-banner{
    background: linear-gradient(360deg, #0F3A5B 19.58%, #9E7140 91.99%), rgba(0, 0, 0, 0.5); 
    height: auto;
    // @media #{variables.$vpMobile600} {
    //   height: 37.25rem;
    // }
    #section1{
      background: none;
    }
    &:not(:has(.banner__img)) + .detail_banner_content {
      padding-top: 1rem;
  }
    .inner-banner-contentWrapper{
      padding-top: 12.9rem;
      @media #{variables.$vpMobile600} {
        padding-top: 9.5rem;
      }
      .top_tagline{
        display: flex;
        gap: 1rem;
        padding-bottom: 1rem;
        @media #{variables.$vpMobile600} {
          padding-bottom: 0.5rem;
        }
      }
      .news_title{
        margin-bottom: 2rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.5rem;
        }
      }
      .banner__img{
        margin-bottom: -12.44rem;
        height: 28.875rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: -3.44rem;
          height: 12rem;
        }
        img{
          border-radius: 1rem;
          width: 100%;
          object-fit: cover;
          height: 100%;
          @media #{variables.$vpMobile600} {
            border-radius: 0.5rem;
          }
        }
      }
    }
    

  }
  &.banner--faq,
  &.banner--listing {
    overflow: hidden;
  }
  &.corporate-banner {
    overflow: hidden;
  }
}
