@use "../global/variables.scss";
.investment-form {
  // z-index: 1;
  // position: relative;
  .title {
    margin-bottom: 4rem;
    color: variables.$sunsetGoldSty2;
    z-index: 1;
    position: relative;
    max-width: 44.875rem;
    &.sty1 {
      color: variables.$blueSty1;
    }
    @media #{variables.$vpMobile600} {
      margin-bottom: 2.5rem;
    }
    span {
      color: variables.$blueSty1;
    }
  }

  .tabs-nav {
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
    margin-bottom: 4rem;
    padding-bottom: 0.5rem;
    @media #{variables.$vpTablet1024} {
      overflow-x: auto;
      margin-inline-end: -2.5rem;
    }
    @media #{variables.$vpMobile600} {
      margin-bottom: 2.5rem;
      margin-inline-end: -1.25rem;
    }
    .tab-link {
      flex: 0 0 19.5%;
      max-width: 19.5%;
      display: flex;
      flex-direction: column;
      align-items: center;
      row-gap: 0.5rem;
      padding: 2rem 1.5rem;
      background: rgba(199, 152, 107, 0.25);
      border-radius: 0.5rem;
      border: 1px solid rgba(255, 255, 255, 0.15);
      overflow: hidden;
      color: white;
      cursor: pointer;
      text-transform: uppercase;
      z-index: 1;
      transition: all ease 0.35s;
      @media #{variables.$vpTablet1024} {
        min-width: 10.625rem;
        width: 100%;
      }
      @media #{variables.$vpMobile600} {
        padding: 1rem 1.5rem;
      }
      .tab-title {
        transition: all ease 0.35s;
        white-space: nowrap;
      }
      &:hover {
        background: variables.$sunsetGoldSty2;
      }
      &.active {
        background: variables.$sunsetGoldSty2;
        .tab-title {
          position: relative;
          transform: translateX(8px);
          &::before {
            position: absolute;
            content: url("/assets/img/icon/diamondIcon.svg");
            width: 0.688rem;
            height: 100%;
            left: -1rem;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
            scale: 1;
            animation: iconAppear 0.35s ease forwards;
          }
        }
      }
    }
  }
  .forms-container {
    position: relative;
    z-index: 1;
    .contact-form {
      .primary-fields {
        display: flex;
        flex-wrap: wrap;
        column-gap: 1.25rem;
        row-gap: 1.5rem;
        @media #{variables.$vpMobile600} {
          row-gap: 1rem;
        }
        .form-group {
          flex: 0 0 49.2%;
          max-width: 49.2%;
          @media #{variables.$vpTablet1199} {
            flex: 0 0 48.9%;
            max-width: 48.9%;
          }
          @media #{variables.$vpMobile600} {
            flex: 0 0 100%;
            max-width: 100%;
          }
        }
      }
      .form-group {
        position: relative;
        display: flex;
        flex-direction: column;
        .input-label {
          margin-bottom: 0.25rem;
          color: variables.$blueSty1;
        }
        .input-txt-field {
          border-radius: 0.625rem;
          border: 1px solid rgba(0, 0, 0, 0.15);
          overflow: hidden;
          background: transparent;
          padding: 1.07rem 0.75rem;
          color: variables.$textStyleSecondary;
          width: 100%;
          @media #{variables.$vpMobile600} {
            padding: 0.89rem 0.75rem;
          }
          &:focus-visible {
            outline: none;
          }
        }
        &.txt-area-body {
          margin-top: 1.5rem;
          @media #{variables.$vpMobile600} {
            margin-top: 1rem;
          }
          .input-txt-field {
            font-family: variables.$secondaryFont;
            /*rtl:raw:font-family: "Zawi"*/
            min-height: 8.9rem;
            padding: 0.8125rem 0.75rem;
            border-radius: 0.625rem;
            overflow: hidden;
            background: transparent;
            padding: 0.8125rem 0.75rem;
            color: variables.$graySty3;
            height: 12rem;
            resize: none;
            width: 100% !important;
          }
          .char-count {
            text-align: right;
            margin-top: 0.5rem;
          }
        }
        .error-message {
          color: variables.$red;
        }
      }
      .submit-btn {
        margin-top: 1.5rem;
        .btn {
          width: 11.125rem;
          height: 2.5rem;
          justify-content: center;
        }
        @media #{variables.$vpMobile600} {
          margin-top: 1rem;
          .btn {
            width: 100%;
            height: 2rem;
          }
        }
      }
      .error-message {
        display: none;
        color: variables.$red;

        &.show {
          display: block;
        }
      }
    }
  }
  .error-message,
  .success-message {
    color: #0d2640 !important;
  }
}

.iti__flag-container {
  /*rtl:raw:right: 0.75rem !important;*/
  /*rtl:raw:left: auto !important;*/
  .iti__selected-flag {
    background: transparent;

    .iti__selected-dial-code {
      margin-left: 0.5rem;
      font-size: 1.125rem;
      font-weight: 400;
      line-height: 1.1;
      color: variables.$blueSty1;
      @media #{variables.$vpMobile600} {
        font-size: 1rem;
      }
    }
    .iti__arrow {
      position: relative;
      width: 0.7rem;
      height: 0.7rem;
      border: 0;
      transition: all ease 0.35s;
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background: url("/assets/img/icon/dropdown-icon-grey.svg");
        background-position: center;
        background-size: cover;
      }
    }
    .iti__arrow--up {
      transform: rotate(180deg);
      border-bottom: 0;
    }
  }
  .iti__country-list {
    border-radius: 0.75rem;
    background: rgba(255, 255, 255, 1);
    backdrop-filter: blur(12px);
    max-height: 170px;
    @media #{variables.$vpTablet1199} {
      max-height: 130px;
    }
    .iti__country {
      color: black;
      .iti__flag-box {
      }
      .iti__country-name {
      }
      .iti__dial-code {
        color: black;
      }
    }
  }
}
@keyframes iconAppear {
  0% {
    opacity: 0;
    transform: translateY(-50%) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translateY(-50%) scale(1);
  }
}
