@use "../global/variables.scss";

.what-we-do-sec {
  z-index: 1;
  position: relative;
  .btn-desk {
    @media #{variables.$vpMobile600} {
      display: none;
    }
  }
  .btn-mob {
    display: none;
    @media #{variables.$vpMobile600} {
      display: block;
      margin-top: 1.5rem;
      display: flex;
      justify-content: center;
    }
  }
  .sub-content-sec {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.25rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
    .sub-heading {
      max-width: 30.25rem;
      .heading-title {
        margin-bottom: 1rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          margin-bottom: 0.5rem;
        }
      }
      .para {
        color: variables.$blueSty1;
      }
    }
  }
  .what-we-do-slider {
    margin-inline: -0.313rem !important;
    @media #{variables.$vpTablet1199} {
      padding-right: 6rem;
    }
    .text-card {
      margin-inline: 0.313rem;
      height: 19.438rem;
      border: 0.762px solid rgba(255, 255, 255, 0.4);
      padding: 1rem;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.4);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      transition: all 0.3s ease-in;

      .icon {
        position: relative;
        width: 2.5rem;
        height: 2.5rem;

        svg {
          width: 2.5rem;
          height: 2.5rem;
          display: block;

          .center {
            opacity: 0;
            transition: opacity 0.3s ease-in;
          }

          .border {
            stroke: black;
            transition: stroke 0.3s ease-in;
          }
        }
      }

      .text-sec {
        position: relative;
        .heading {
          transform: translateY(0);
          transition: all 0.3s ease-in;
          color: variables.$blueSty1;
        }
        .para {
          opacity: 0;
          transform: translateY(20px);
          max-height: 0;
          overflow: hidden;
          color: variables.$blueSty1;
          transition: all 0.3s ease-in;
        }
      }

      &:hover {
        background: white;
        @media #{variables.$vpTablet1199} {
          background: rgba(255, 255, 255, 0.4);
        }
        svg {
          .center {
            opacity: 1;
            @media #{variables.$vpTablet1199} {
              opacity: 0;
            }
          }

          .border {
            stroke: variables.$sunsetGoldSty2;
            @media #{variables.$vpTablet1199} {
              stroke: variables.$blueSty1;
            }
          }
        }

        .text-sec {
          .heading {
            transform: translateY(0);
            margin-bottom: 1rem;
            color: variables.$sunsetGoldSty2;
          }
          .para {
            opacity: 1;
            transform: translateY(0);
            max-height: 6rem;
          }
          @media #{variables.$vpTablet1199} {
            .heading {
              transform: translateY(0);
              margin-bottom: 0;
              color: variables.$blueSty1;
            }
            .para {
              opacity: 0;
              transform: translateY(20px);
              max-height: 0;
              overflow: hidden;
            }
          }
        }
      }
      &.slick-active {
        @media #{variables.$vpTablet1199} {
          background: white;
          svg {
            .center {
              opacity: 1;
            }

            .border {
              stroke: variables.$sunsetGoldSty2;
            }
          }

          .text-sec {
            .heading {
              transform: translateY(0);
              margin-bottom: 1rem;
              color: variables.$sunsetGoldSty2;
            }
            .para {
              opacity: 1;
              transform: translateY(0);
              max-height: 6rem;
            }
          }
        }
      }
    }
    .slick-list {
      overflow: visible !important;
    }
    .slick-track {
      margin-inline-start: 0;
    }
  }
  .custom-arrows-pagination {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.5rem;
    column-gap: 1.5rem;

    @media #{variables.$vpMobile600} {
      justify-content: space-between;
      margin-top: 2rem;
    }

    &.hide {
      display: none;
    }
    &.show {
      display: flex;
    }
    .slide-count {
      display: flex;
      align-items: center;
      column-gap: 0.3rem;
      /*rtl:raw:flex-direction: row-reverse;*/
      .current-slide-what-we-do {
        color: variables.$sunsetGoldSty2;
      }
      .total-slides-what-we-do {
        color: rgba(13, 38, 64, 0.2);
      }
      .sep-slide {
        color: rgba(13, 38, 64, 0.2);
      }
    }
    .arrows-wrapper {
      display: flex;
      column-gap: 0.5rem;
      @media #{variables.$vpMobile600} {
        column-gap: 0.25rem;
      }
      .custom-prev-what-we-do,
      .custom-next-what-we-do {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.7px solid rgba(0, 0, 0, 0.15);
        background: rgba(199, 152, 107, 0.1);
        border-radius: 50%;
        overflow: hidden;
        width: 3rem;
        height: 3rem;
        /*rtl:raw:transform: rotate(-180deg);*/
        cursor: pointer;
        transition: all ease 0.35s;
        @media #{variables.$vpTablet1199} {
          &:hover {
            background: rgba(199, 152, 107, 0.1) !important;
          }
        }
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg {
          width: 1rem;
          height: 1rem;
          path {
            transition: all ease 0.35s;
          }
        }
        &:hover {
          border: 0.7px solid variables.$sunsetGoldSty2;
          svg {
            path {
              fill: variables.$sunsetGoldSty2;
              @media #{variables.$vpTablet1199} {
                fill: #292d32 !important;
              }
            }
          }
        }
        /* Disable hover effects below 600px */
        @media #{variables.$vpTablet1199} {
          &:hover {
            border: 0.7px solid rgba(0, 0, 0, 0.15);

            svg {
              path {
                fill: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
  &.core-competencies {
