@use "../global/variables.scss";

.investment-opportunities {
  background-color: variables.$graySty6;
  .wrapper-box {
    .media-gallery-box {
      position: relative;

      .media-listing {
        &.list-view-listing {
          display: flex;
          // gap: 2rem;
          flex-direction: column;
          flex-wrap: wrap;
          @media #{variables.$vpMobile600} {
            gap: 0.625rem;
          }

          .listingWrapper {
            flex: 0 0 100%;
            max-width: 100%;
            margin: 0;
            padding: 0.625rem 0 0;
            background: transparent;
            width: 100%;
            height: auto;
            // .list-items {
            //     &.grid-view-item {
            //         display: none !important;
            //     }
            // }

            @media #{variables.$vpMobile600} {
              padding: 0;
              &:first-child {
                padding-top: 0.625rem;
              }
            }
            .grid-view-item {
              display: flex !important;
              align-items: center;
              width: 100%;
              // border: 0.063rem solid $secondarySemiLight;
              border-radius: 1rem;
              overflow: hidden;
              cursor: pointer;
              height: 14.375rem;

              @media #{variables.$vpMobile600} {
                border-radius: 0.5rem;
                padding: 0.5rem;
                align-items: flex-start;
                gap: 0;
                height: auto;
                background-color: white;
              }
              .img-sec {
                border-radius: 1rem 0 0 1rem;
                width: 26.25rem;
                flex: 0 0 26.25rem;
                @media #{variables.$vpTablet1199} {
                  height: 16.3rem;
                  width: 18.25rem;
                  flex: 0 0 18.25rem;
                }
                @media #{variables.$vpMobile600} {
                  height: 5.875rem;
                  max-width: 23.6%;
                  flex: 0 0 23.6%;
                  border-radius: 0.5rem;
                }
                img {
                  border-radius: 1rem 0 0 1rem;
                  @media #{variables.$vpMobile600} {
                    border-radius: 0.5rem;
                  }
                }
              }
              .content_box {
                height: 100%;
                gap: 0.5rem;
                border-radius: 0 1rem 1rem 0;
                display: flex;
                width: 67.5%;
                flex-direction: column;
                align-items: flex-start;
                @media #{variables.$vpTablet1199} {
                  width: 73%;
                }
                @media #{variables.$vpMobile600} {
                  width: 100%;
                }
                .main-title {
                  .title {
                    padding-top: 0.5rem;
                    @media #{variables.$vpMobile600} {
                      padding-top: 0.25rem;
                    }
                    .card_main_title {
                      // @media #{variables.$vpMobile600} {
                      //    font-size: 1rem;
                      //    line-height: 1.1rem;
                      //   }
                    }
                  }
                }
                //   align-items: center;
                //   justify-content: space-between;
                .content_box__left {
                  max-width: 70%;
                  flex: 0 0 70%;
                  @media #{variables.$vpTablet1199} {
                    max-width: 60%;
                    flex: 0 0 60%;
                  }
                  @media #{variables.$vpMobile600} {
                    max-width: 100%;
                    flex: 0 0 100%;
                  }
                }
                .decription_wrapper {
                  flex-direction: row;
                  @media #{variables.$vpMobile600} {
                    flex-direction: column;
                  }
                  .content__wrap {
                    @media #{variables.$vpMobile600} {
                      gap: 0.25rem;
                      display: flex;
                      flex-direction: column;
                    }
                  }
                  .action_btn {
                    justify-content: flex-end;
                    .btn_wrap {
                      .btn {
                        &:hover {
                          @media #{variables.$vpTablet1199} {
                            background: transparent !important;
                          }
                        }
                      }
                    }
                  }
                }

                @media #{variables.$vpMobile600} {
                  max-width: 100%;
                  // flex: 0 0 76.4%;
                  gap: 0.25rem;
                  padding: 0 1rem;
                  flex-direction: column;
                }
                .pera-sec {
                  max-width: 75%;
                  @media #{variables.$vpMobile600} {
                    max-width: 100%;
                  }
                }
                .meta_wrapper {
                  margin-top: 3.9rem;
                  justify-content: flex-end;
                  @media #{variables.$vpMobile600} {
                    margin-top: 1rem;
                  }
                  .action_btn {
                    .btn_wrap {
                      .btn_Icon {
                        @media #{variables.$vpMobile600} {
                          padding: 0.75rem;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      .sec-title {
        margin-bottom: 1.9rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          margin-bottom: 1.375rem;
        }
      }
    }
  }
}

.forms-container-modal {
  margin-top: 4rem;
  position: relative;
  z-index: 1;
  @media #{variables.$vpMobile600} {
    margin-top: 3rem;
  }
  .contact-form {
    .primary-fields {
      display: flex;
      flex-wrap: wrap;
      column-gap: 1.25rem;
      row-gap: 1.5rem;
      @media #{variables.$vpMobile600} {
        row-gap: 1rem;
      }
      .form-group {
        flex: 0 0 48%;
        max-width: 48%;
        @media #{variables.$vpTablet1199} {
          flex: 0 0 48%;
          max-width: 48%;
        }
        @media #{variables.$vpMobile600} {
          flex: 0 0 100%;
          max-width: 100%;
        }
      }
    }
    .form-group {
      position: relative;
      display: flex;
      flex-direction: column;
      .input-label {
        margin-bottom: 0.25rem;
        color: variables.$blueSty1;
      }
      .input-txt-field {
        border-radius: 0.625rem;
        border: 1px solid rgba(0, 0, 0, 0.15);
        overflow: hidden;
        background: transparent;
        padding: 1.07rem 0.75rem;
        color: variables.$textStyleSecondary;
        width: 100%;
        @media #{variables.$vpMobile600} {
          padding: 0.89rem 0.75rem;
        }
        &:focus-visible {
          outline: none;
        }
      }
      &.txt-area-body {
        margin-top: 1.5rem;
        @media #{variables.$vpMobile600} {
          margin-top: 1rem;
        }
        .input-txt-field {
          font-family: variables.$secondaryFont;
          /*rtl:raw:font-family: "Zawi"*/
          min-height: 8.9rem;
          padding: 0.8125rem 0.75rem;
          border-radius: 0.625rem;
          overflow: hidden;
          background: transparent;
          padding: 0.8125rem 0.75rem;
          height: 12rem;
          resize: none;
          width: 100% !important;
        }
        .char-count {
          text-align: right;
          margin-top: 0.5rem;
        }
      }
      .error-message {
        color: variables.$red;
      }
    }
    .submit-btn {
      margin-top: 1.5rem;
      .btn {
        width: 11.125rem;
        height: 2.5rem;
        justify-content: center;
      }
      @media #{variables.$vpMobile600} {
        margin-top: 1rem;
        .btn {
          width: 100%;
          height: 2rem;
        }
      }
    }

    .error-message,
    .success-message {
      color: variables.$blueSty1 !important;
    }
    .error-message {
      display: none;
      color: variables.$red;

      &.show {
        display: block;
      }
    }
  }
}
