@use "../global/variables.scss";

.our-objective-sec{
  z-index: 1;
  position: relative;
  .btn-desk {
    @media #{variables.$vpMobile600} {
      display: none;
    }
  }
  .btn-mob {
    display: none;
    @media #{variables.$vpMobile600} {
      display: block;
      margin-top: 1.5rem;
      display: flex;
      justify-content: center;
    }
  }
  .sub-content-sec {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.5rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
    .sub-heading {
      max-width: 30.25rem;
      .heading-title {
        color: variables.$blueSty1;
      }
      .para {
        color: variables.$blueSty1;
      }
    }
  }
  .our-objective-slider {
    margin-inline: -0.313rem !important;
    @media #{variables.$vpMobile600} {
      padding-right: 6.25rem;
    }
    .text-card {
      margin-inline: 0.313rem;
      height: 19.438rem;
      border: 0.762px solid rgba(255, 255, 255, 0.4);
      padding: 1rem;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.4);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      transition: all 0.15s ease;

      .icon {
        position: relative;
        width: 2.5rem;
        height: 2.5rem;

        svg {
          width: 2.5rem;
          height: 2.5rem;
          display: block;

          .center {
            opacity: 0;
            transition: opacity 0.15s ease;
          }

          .border {
            stroke: black;
            transition: stroke 0.15s ease;
          }
        }
      }

      .text-sec {
        position: relative;
        .heading {
          transform: translateY(0);
          transition: transform 0.15s ease, margin 0.15s ease;
          color: variables.$blueSty1;
          @media #{variables.$vpMobile600} {
            transform: translateY(0);
            margin-bottom: 1rem;
          }
        }
        .para {
          opacity: 0;
          transform: translateY(20px);
          max-height: 0;
          overflow: hidden;
          color: variables.$blueSty1;
          transition: opacity 0.15s ease, transform 0.15s ease,
            max-height 1s ease;
          @media #{variables.$vpMobile600} {
            opacity: 1;
            transform: translateY(0);
            max-height: 6rem;
          }
        }
      }

      &:hover {
        background: white;
        @media #{variables.$vpMobile600} {
          background: rgba(255, 255, 255, 0.4);
        }
        svg {
          .center {
            opacity: 1;
          }

          .border {
            stroke: variables.$sunsetGoldSty2;
          }
        }

        .text-sec {
          .heading {
            transform: translateY(0);
            margin-bottom: 1rem;
            color: variables.$sunsetGoldSty2;
          }
          .para {
            opacity: 1;
            transform: translateY(0);
            max-height: 6rem;
          }
        }
      }
      &.slick-active {
        @media #{variables.$vpMobile600} {
          background: white;
          svg {
            .center {
              opacity: 1;
            }

            .border {
              stroke: variables.$sunsetGoldSty2;
            }
          }

          .text-sec {
            .heading {
              transform: translateY(0);
              margin-bottom: 1rem;
              color: variables.$sunsetGoldSty2;
            }
            .para {
              opacity: 1;
              transform: translateY(0);
              max-height: 6rem;
            }
          }
        }
      }
    }
    .slick-list {
      overflow: visible !important;
    }
  }
  .custom-arrows-our-objective {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.5rem;
    column-gap: 1.5rem;

    @media #{variables.$vpMobile600} {
      justify-content: space-between;
      margin-top: 2rem;
    }
    .slide-count {
      display: flex;
      align-items: center;
      column-gap: 0.3rem;
      /*rtl:raw:flex-direction: row-reverse;*/
      .current-slide-our-objective {
        color: variables.$sunsetGoldSty2;
      }
      .total-slides-our-objective {
        color: rgba(13, 38, 64, 0.2);
      }
      .sep-slide {
        color: rgba(13, 38, 64, 0.2);
      }
    }
    .arrows-wrapper {
      display: flex;
      column-gap: 0.5rem;
      @media #{variables.$vpMobile600} {
        column-gap: 0.25rem;
      }
      .custom-prev-our-objective,
      .custom-next-our-objective {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.7px solid rgba(0, 0, 0, 0.15);
        background: rgba(199, 152, 107, 0.1);
        border-radius: 50%;
        overflow: hidden;
        width: 3rem;
        height: 3rem;
        /*rtl:raw:transform: rotate(-180deg);*/
        cursor: pointer;
        transition: all ease 0.35s;
        @media #{variables.$vpTablet1199} {
          &:hover {
            background: rgba(199, 152, 107, 0.1) !important;
          }
        }
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg {
          width: 1rem;
          height: 1rem;
          path {
            transition: all ease 0.35s;
          }
        }
        &:hover {
          border: 0.7px solid variables.$sunsetGoldSty2;
          svg {
            path {
              fill: variables.$sunsetGoldSty2;
              @media #{variables.$vpTablet1199} {
                fill: #292D32 !important;
              }
            }
          }
        }
        /* Disable hover effects below 600px */
        @media #{variables.$vpTablet1199} {
          &:hover {
            border: 0.7px solid rgba(0, 0, 0, 0.15);

            svg {
              path {
                fill: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
}
