@use "../global/variables.scss";

#section2 {
  // background: linear-gradient(0deg, #0F3A5B 19.58%, #9E7140 91.99%), linear-gradient(123deg, #061930 30.48%, #0F3A5B 101.87%);
  color: white;
  position: relative;
  padding-top: 11.19rem;
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: -0.1rem;
  z-index: 1;
  // @media #{variables.$vpTablet1199} {
  //   padding: 0 2.5rem;
  // }
  @media #{variables.$vpMobile600} {
    padding-top: 8.32rem;
    padding-bottom: 4.25rem;
  }
  .overline {
    text-transform: uppercase;
  }
  .paragraph {
    max-width: 62.75rem;
    margin-top: 0.75rem;
    margin-bottom: 2rem;

    @media #{variables.$vpTablet1199} {
      max-width: 100%;
      padding: 0 2.5rem;
    }
    @media #{variables.$vpMobile600} {
      padding: 0 1.25rem;
      margin-top: 0.5rem;
      margin-bottom: 1.5rem;
    }
    span{
      color: variables.$sunriseGold;
    }
  }

  .vertical-line-wrapper {
    margin-top: 7.5rem;
    position: relative;
    @media #{variables.$vpMobile600} {
      display: none;
    }
  }
  .line-wrapper {
    overflow: visible !important;
  }
  .sec-description {
    max-width: 25.8125rem;
    @media #{variables.$vpMobile600} {
      max-width: 100%;
      padding: 0 1.25rem;
    }
  }
}

.vertical-line-2,
.vertical-line-3 {
  width: 1px;
  height: 0%;
  background-color: white;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  z-index: 2;

  .icon-1 {
    position: absolute;
    left: -0.25rem;
    top: -2px;
    will-change: transform;
    transform: translateZ(0); 
  }
  .icon-2 {
    position: absolute;
    left: -0.25rem;
    top: 10%;
    will-change: transform;
    transform: translateZ(0);
  }
  .icon-3 {
    position: absolute;
    left: -0.25rem;
    bottom: -2px;
    will-change: transform;
    transform: translateZ(0);
  }
}
