@use "../global/variables.scss";

.investment-slider-sec {
  position: relative;
  z-index: 1;
  padding-top: 5.938rem;
  @media #{variables.$vpMobile600} {
    padding-top: 1.438rem;
  }
  .sub-content-sec {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.25rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
    .sub-heading {
      max-width: 30.25rem;
      .heading-title {
        margin-bottom: 1rem;
        color: white;
      }
      .para {
        color: white;
      }
    }
  }
  .investment-slider {
    margin-inline: -0.313rem !important;
    @media #{variables.$vpTablet1199} {
      padding-right: 6.25rem;
    }
    .text-card {
      margin-inline: 0.313rem;
      height: 19.438rem;
      border: 0.762px solid rgba(255, 255, 255, 0.4);
      padding: 1rem;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.1);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      transition: all 0.15s ease;

      .icon {
        position: relative;
        width: 2.5rem;
        height: 2.5rem;

        svg {
          width: 2.5rem;
          height: 2.5rem;
          display: block;

          .center {
            opacity: 0;
            transition: opacity 0.15s ease;
          
          }

          .border {
            stroke: variables.$white;
            transition: stroke 0.15s ease;
          }
        }
      }

      .text-sec {
        position: relative;
        .heading {
          color: white;
          transform: translateY(0);
          transition: transform 0.15s ease, margin 0.15s ease, color 0.15s ease;
        }
        .para {
          opacity: 0;
          transform: translateY(20px);
          max-height: 0;
          overflow: hidden;
          transition: opacity 0.15s ease, transform 0.15s ease,
            max-height 1s ease;
        }
      }

      &:hover {
        background: white;
        @media #{variables.$vpTablet1199} {
          background: rgba(255, 255, 255, 0.4);
        }
        svg {
          .center {
            opacity: 1;
            @media #{variables.$vpTablet1199} {
              opacity: 0;
            }
          }

          .border {
            stroke: variables.$sunsetGoldSty2;
            @media #{variables.$vpTablet1199} {
              stroke: variables.$white;
            }
          }
        }

        .text-sec {
          .heading {
            transform: translateY(0);
            margin-bottom: 1rem;
            color: variables.$sunsetGoldSty2;
            @media #{variables.$vpTablet1199} {
              color: white;
              margin-bottom: 0;
            }
          }
          .para {
            opacity: 1;
            transform: translateY(0);
            max-height: 6rem;
            color: variables.$blueSty1;
            @media #{variables.$vpTablet1199} {
              transform: translateY(20px);
              transform: translateY(20px);
              max-height: 0;
              overflow: hidden;
              opacity: 0;
            }
          }
        }
      }
      &.slick-active {
        @media #{variables.$vpTablet1199} {
          background: variables.$white;
          .text-sec {
            position: relative;
            .heading {
              @media #{variables.$vpTablet1199} {
                transform: translateY(0);
                margin-bottom: 1rem;
                color: variables.$sunsetGoldSty2;
                margin-bottom: 0.5rem;
              }
            }
            .para {
                @media #{variables.$vpTablet1199} {
                opacity: 1;
                transform: translateY(0);
                max-height: 6rem;
                color: variables.$blueSty1;
              }
            }
          }
          svg {
            .center {
              opacity: 1;
            }
            .border {
              stroke: variables.$sunsetGoldSty2;
            }
          }
        }
      }
    }
    .slick-list {
      overflow: visible !important;
    }
  }
  .custom-arrows-pagination {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.5rem;
    column-gap: 1.5rem;

    @media #{variables.$vpMobile600} {
      justify-content: space-between;
      margin-top: 2rem;
    }
    .slide-count {
      display: flex;
      align-items: center;
      column-gap: 0.3rem;
      /*rtl:raw:flex-direction: row-reverse;*/
      .current-slide-what-we-do {
        color: variables.$sunsetGoldSty2;
      }
      .total-slides-what-we-do {
        color: variables.$backgroundLight;
      }
      .sep-slide {
        color: variables.$backgroundLight;
      }
    }
    .arrows-wrapper {
      display: flex;
      column-gap: 0.5rem;
      @media #{variables.$vpMobile600} {
        column-gap: 0.25rem;
      }
      .custom-prev-what-we-do,
      .custom-next-what-we-do {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.75px solid rgba(255, 255, 255, 0.15);
        background: rgba(255, 255, 255, 0.10);
        border-radius: 50%;
        overflow: hidden;
        width: 3rem;
        height: 3rem;
        /*rtl:raw:transform: rotate(-180deg);*/
        cursor: pointer;
        transition: all ease 0.35s;
        @media #{variables.$vpTablet1199} {
          &:hover {
            background: rgba(255, 255, 255, 0.10) !important;
          }
        }
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg {
          width: 1rem;
          height: 1rem;
          path {
            transition: all ease 0.35s;
            fill: white;
          }
        }
        &:hover {
          border: 0.7px solid variables.$sunsetGoldSty2;
          svg {
            path {
              fill: variables.$sunsetGoldSty2;
            }
          }
        }
        /* Disable hover effects below 600px */
        @media #{variables.$vpTablet1199} {
          &:hover {
            border: .75px solid hsla(0, 0%, 100%, .15);

            svg {
              path {
                fill: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
}
