@use "../global/variables.scss";

.banner-wrapper {
  position: relative;
  height: 100vh;
  max-height: 100vh;
  overflow: visible;
  .video--wrapper {
    height: 100%;
    position: absolute;
    width: 100%;
    top: -1px;
    left: 0;
    .my-video-js {
      width: 100%;
      height: 100vh;
      video{
        object-fit: cover;
      }
      object-fit: cover;
    }
  }
  .img--wrapper {
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  #section1 {
    color: white;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    // background: linear-gradient(180deg, rgba(158, 113, 64, 0.00) 50%, #9E7140 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%);
    background: 
    linear-gradient(180deg, rgba(158, 113, 64, 0) 50%, #9E7140 100%), 
    linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);
    .text-container {
      white-space: nowrap;
      overflow: hidden;
      width: 100vw;
      position: relative;
      will-change: transform;
      transform: translateZ(0);
      margin-bottom: 2.34rem;
      @media #{variables.$vpMobile600} {
        margin-bottom: 1.54rem;
      }
    }

    .scrolling-text-animation {
      display: inline-block;
      white-space: nowrap;
      will-change: transform;
      transform: translateZ(0);
      margin: 0;
      line-height: 1.2;
    }
    .bottom-wrapper {
      position: relative;
      padding: 0 5rem;
      min-height: 11.23rem;
      // margin: 0 auto;
      @media #{variables.$vpTablet1199} {
        padding: 0 2.5rem;
      }
      @media #{variables.$vpMobile600} {
        padding: 0 1.25rem;
        min-height: 5.59rem;
      }
      .bottom-content {
        display: flex;
        flex-direction: row;
        // justify-content: space-between;
        padding-top: 2.41rem;
        padding-bottom: 5.06rem;

        @media #{variables.$vpMobile600} {
          // display: none;
          padding-bottom: 3.06rem;
        }
        .sub-heading {
          width: 50%;
          text-transform: uppercase;
          // padding-left: 5rem;
          // @media #{variables.$vpTablet1199} {
          //   padding: 0 2.5rem;
          // }
        }
        .description {
          width: 50%;
          padding-left: 2.81rem;
          padding-right: 11.81rem;
          @media #{variables.$vpTablet1199} {
            padding-left: 0 2.5rem;
            padding-right: 0rem;
          }
          @media #{variables.$vpMobile600} {
            display: none;
          }
        }
      }
    }

    .description-moblie {
      display: none;
      @media #{variables.$vpMobile600} {
        padding-right: 1.25rem;
        padding-left: 5.75rem;
        padding-bottom: 0.75rem;
        display: block;
      }
    }
  }

  .vertical-line-1 {
    width: 1px;
    height: 0%;
    background-color: white;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: 2;

    .icon-1 {
      position: absolute;
      left: -0.28rem;
      top: 14%;
      will-change: transform;
      transform: translateZ(0); 
    }
    .icon-2 {
      position: absolute;
      left: -0.28rem;
      bottom: 10%;
      will-change: transform;
      transform: translateZ(0);
    }
    .icon-3 {
      position: absolute;
      left: -0.28rem;
      bottom: -2px;
      will-change: transform;
      transform: translateZ(0);
    }
  }
}
