@use "./variables";

:root {
  --vh-unit: 1vh;
  --vw-unit: 1vw;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-family: variables.$secondaryFont;
  font-size: 1.111111111vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  @media #{variables.$vpTablet1199} {
    font-size: 1.3625vw;
  }
  @media #{variables.$vpTablet991} {
    font-size: 1.514530777vw;
  }
  @media #{variables.$vpMobile600} {
    font-size: 4.102564103vw;
  }
}
body {
  font-family: variables.$secondaryFont;
  min-height: 100vh;
  width: 100%;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
  // overscroll-behavior: none;
}

input {
  font-family: variables.$secondaryFont;
}

.content {
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
  position: relative;
  touch-action: none;
}


.container {
  padding: 0 5rem;
  margin: 0 auto;
  width: 100%;
  @media #{variables.$vpTablet1199} {
    padding: 0 2.5rem;
  }
  @media #{variables.$vpMobile600} {
    padding: 0 1.25rem;
  }
  &--left {
    padding-left: 5rem;
    @media #{variables.$vpTablet1199} {
      padding-left: 2.5rem;
    }
    @media #{variables.$vpMobile600} {
      padding-left: 1.25rem;
    }
  }

  &--right {
    padding-right: 5rem;
    @media #{variables.$vpTablet1199} {
      padding-right: 2.5rem;
    }
    @media #{variables.$vpMobile600} {
      padding-right: 1.25rem;
    }
  }
}
.sec-padded {
  padding: 5rem 0;
  @media #{variables.$vpMobile600} {
    padding: 2.5rem 0;
  }

  &--top {
    padding-top: 5rem;
    @media #{variables.$vpMobile600} {
      padding-top: 2.5rem;
    }
  }

  &--bottom {
    padding-bottom: 5rem;
    @media #{variables.$vpMobile600} {
      padding-bottom: 2.5rem;
    }
  }
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: variables.$white;
  &.undeline {
    text-decoration: underline;
  }
}
.txt-white {
  color: variables.$white;
}
.text-bold {
  font-weight: 700;
}

.uppercase {
  text-transform: uppercase;
}
img {
  max-width: 100%;
}

b,
strong {
  font-weight: 700;
}
U {
  text-decoration: underline;
}
.visibility_none {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.txt-center {
  text-align: center;
}

.line-wrapper {
  display: block;
  // overflow: visible !important;
}

.getInTouch {
  padding: 0.94rem 1.5rem;
  border-radius: 9.375rem;
  border: 0.75px solid rgba(255, 255, 255, 0.15);
  background: rgba(199, 152, 107, 0.1);
  a {
    text-transform: uppercase;
    letter-spacing: 0.04rem;
  }
}
.primary_btn {
  padding: 0.94rem 1.5rem;
  border-radius: 9.375rem;
  border: 0.75px solid rgba(0, 0, 0, 0.15);
  background: rgba(199, 152, 107, 0.1);
  width: max-content;
  a {
    text-transform: uppercase;
    letter-spacing: 0.04rem;
    color: #0d2640;
    font-weight: 700;
    @media #{variables.$vpMobile600} {
      font-size: 0.875rem;
      letter-spacing: 0.035rem;
      line-height: 1.00625rem;
    }
  }
  @media #{variables.$vpMobile600} {
    padding: 0.75rem 1rem;
  }
}

.slick-slide {
  visibility: visible !important;
}
.slick-cloned {
  visibility: hidden;
}

.horizontal-line {
  position: relative;
  width: 0%;
  height: 1px;
  background-color: white;
  // position: absolute;
  // top: 70%; // Center vertically
  // left: 0;
  // transform: translateY(-50%);

  .icon-1 {
    position: absolute;
    left: -2px;
    top: -0.25rem;
  }
  .icon-2 {
    position: absolute;
    left: 5%;
    top: -0.25rem;
    @media #{variables.$vpMobile600} {
      left: 10%;
    }
  }
  .icon-3 {
    position: absolute;
    left: 20%;
    top: -0.25rem;
    @media #{variables.$vpMobile600} {
      left: auto;
      right: 10%;
    }
  }
}

.diamond-icon-horizontal {
  width: 0.625rem;
  height: 0.62363rem;
  flex-shrink: 0;
  will-change: transform;
  transform: translateZ(0);
  opacity: 0; // Start invisible
  transition: opacity 0.3s ease;
}

.diamond-icon-vertical {
  width: 0.563rem;
  height: 0.563rem;
  flex-shrink: 0;
  will-change: transform;
  transform: translateZ(0);
  opacity: 0; // Start invisible
  transition: opacity 0.3s ease;
}

.bg--wrapper {
 margin-top: -1px;
  background: linear-gradient(1deg, #061930 0, #0f3a5b 63.7%, #9e7140 89%);
  position: relative;
  &.sty2 {
    background: linear-gradient(1deg, #061930, #0f3a5b 36.7%, #9e7140 76%);
  }
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: url(/assets/img/bg-elements/dots.png);
    background-repeat: repeat;
    opacity: 0.3;
  }
  .bg-elements {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    .top-element {
      position: absolute;
      left: 11.3%;
      top: 16%;
      // animation: spin 20s linear infinite;
      	/*rtl:raw:left: 11.3%; right: auto;*/
        @media #{variables.$vpTablet991} {
          top: 13%;
          width: 7.20025rem;
          height: 7.21806rem;
        }
    }
    .right-element {
      position: absolute;
      right: 0;
      bottom: 18%;
      	/*rtl:raw:right: 0; left: auto;*/
    }
    .right-element2 {
      position: absolute;
      right: 0;
      bottom: 0;
      /*rtl:raw:right: 0%; left: auto;*/
    }
    .bottom-element {
      position: absolute;
      bottom: 0;
      left: 0;
      	/*rtl:raw:left: 0; right: auto;*/
    }
  }
}
.bg-wrapper2 {
  overflow: hidden;
  background: linear-gradient(
      180deg,
      rgba(249, 247, 243, 0) 59.48%,
      #f9f7f3 68.54%
    ),#f9f7f3;
  position: relative;
  &.sty1 {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 30.51%, #FFF 39%), linear-gradient(244deg, #FFF 51.47%, #F1EEEA 103.29%), #FFF;
  }
  .bg-elements {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    .top-element {
      position: absolute;
      top: 0;
      left: 0;
      /*rtl:raw:left: 0; right: auto;*/
      .mbl {
        display: none;
      }
      @media #{variables.$vpMobile600} {
        .mbl {
          display: block;
        } 
        .desktop {
          display: none;
        }
      }
    }
    .bottom-element {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      /*rtl:raw:left: 0; right: auto;*/
    }
    .diamond-element {
      position: absolute;
      right: 10%;
      bottom: 5%;
      z-index: 1;
      /*rtl:raw:right: 10%; left: auto;*/
    }
    .right-element {
      position: absolute;
      right: 0;
      top: 20%;
      /*rtl:raw:right: 0%; left: auto;*/
    }
    .right-bottom-element {
      position: absolute;
      right: 0;
      bottom: -12%;
      /*rtl:raw:right: 0%; left: auto;*/
    }
    .left-middle-element {
      position: absolute;
      left: 0;
      top: 12.37rem;
      /*rtl:raw:right: auto; left: 0*/
    }
    .left-element {
      position: absolute;
      left: 0;
      bottom: 0;
      /*rtl:raw:right: auto; left: 0*/
    }
    .sec-bg {
      position: absolute;
      top: 0;
      right: 0;
      width: 91.75rem;
      height: 67.5rem;
      /*rtl:raw:right: 0%; left: auto;*/
    }
  }
}
.field-margin-top {
  margin-top: 1.5rem;
  row-gap: 1rem;
}
.tag{
  padding: 0.25rem 0.5rem;
  background-color: variables.$graySty6;
  border-radius: 0.5rem;
  width: auto;
  @media #{variables.$vpMobile600} {
    border-radius: 0.25rem;
  }
  .tag--label{
    color: variables.$textStyleSecondary;
    text-transform: uppercase;
  }
  &.no_bg{
    background-color: transparent;
    .tag--label{
      color: variables.$graySty3;
    }
  }
}
.inner-banner-contentWrapper{
  max-width: 52.75rem;
  margin: 0 auto;
}


@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.theme--dark {
  .heading1,
  .heading2,
  .heading3,
  .heading4,
  .heading5,
  .heading6,
  .body1,
  .body2,
  a,
  p,
  li {
    color: variables.$white;
  }
}
.open-modal-btn {
  cursor: pointer;
}
.open-modal-btn-from {
  cursor: pointer;
}
.btn_wrap{
  .btn_Icon{
    p{
      color: variables.$blueSty1;
      font-weight: 700;
      transition: all .5s ease;
    }
    .cta-txt{
      color: variables.$blueSty1;
      font-weight: 700;
      transition: all .5s ease;
    }
    .download_iconBox{
      svg{
        path{
          transition: all .5s ease;
          
        }
      }
    }
    &:hover{
      p{
        color: white;
        @media #{variables.$vpTablet1199} {
          color:  variables.$blueSty1
        }
      }
      .cta-txt{
        color: white;
        @media #{variables.$vpTablet1199} {
          color:  variables.$blueSty1
        }
      }
      .download_iconBox{
        svg{
          path{
            stroke: white;
            @media #{variables.$vpTablet1199} {
              stroke: variables.$blueSty1;
            }
          }
        }
      }
    }
  }
}
.content--page{
 
      @media #{variables.$vpMobile600} {
        padding-bottom: 4rem;
      }
      h4{
        padding-bottom: 0.5rem;
        padding-top: 5rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          padding-top: 4rem;
          
        }
      }
      p{
        padding-top: 1.5rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          padding-top: 1rem !important;
        }
        &:first-child{
          padding-top: 0;
        }
        &:last-child{
          margin-bottom: 0;
        }
      }
      
      ol{
        margin: 1.5rem 0 0 0;
        padding:  0 0 0 0.7rem;
        list-style: auto;
        li{
            padding-bottom: 0;
            color:  variables.$blueSty1;
            font-size: 1.125rem;
            font-weight: 400;
            line-height: 1.1;
            padding-left: 0.2rem;
            @media #{variables.$vpMobile600} {
              font-size: 1rem;
            }
            a{
              margin-top: 0;
            }

        }
    }
    a{
      text-decoration: none;
      color: variables.$sunriseGold;
      font-size: 1.125rem;
      font-weight: 400;
      line-height: 1.1;
      margin-top: 1.5rem;
      display: inline-block;
      width: max-content;
      position: relative;
      @media #{variables.$vpMobile600} {
        margin-top: 1rem;
        font-size: 1rem;
      }
      &::after{
        content: '';
        position: absolute;
        bottom: 2px;
        left: 0;
        height: 1px;
        width: 0;
        background: variables.$sunriseGold;
        transition: all 0.5s ease;
      }
      &:hover{
        &::after{
          width: 100%;
        }
      }
    }
    ul{
      margin: 1.5rem 0 0 0;
      padding:  0 0 0 1rem;
      // list-style: auto;
      li{
          padding-bottom: 0;
          color:  variables.$blueSty1;
          position: relative;
          font-size: 1.125rem;
          font-weight: 400;
          line-height: 1.1;
          a{
            margin-top: 0;
          }
          @media #{variables.$vpMobile600} {
            font-size: 1rem;
          }
          
          &::before{
            content: '';
            position: absolute;
            top: 50%;
            left: -1rem;
            width: 0.35rem;
            height: 0.35rem;
            background-color: #CFA474;
            z-index: 1;
            transform: translateY(-50%) rotate(45deg);

          }

      }
  }
}

.header-light{
  .sec-header{
    .header-wrapper{
     .postBox{
      .logo-box{
        a{
          svg{
            path{
              fill: variables.$blueSty1;
            }
          }
        }
      }
      .menu-wrapper{
        .menu-box{
          .menu-item{
            .cta-txt{
              color: variables.$blueSty1;
            }
            svg{
              path{
                stroke: variables.$blueSty1;
              }
            }
          }
          &::after{
            background: variables.$blueSty1;
          }
        }
        .menu-secondary{
          .cta-txt, .btn{
            color: variables.$blueSty1;
          }
          .hamburger--wrapper{
            span{
              background-color: variables.$blueSty1;
            }
          }
        }
      }
     }
    }
  }
}
.desktop-only {
  @media #{variables.$vpMobile600} {
    display: none;
  }
}

.fancybox-container{
    background: rgba(0,0,0,.8);
    backdrop-filter: blur(6px);
   .fancybox-bg{
    background: none;
    
  }
  .fancybox-inner{
    .fancybox-infobar{
      position: absolute;
      bottom: 3.81rem;
      transform: translateX(-50%);
      left: 50%;
      top: auto;
      visibility: visible;
      opacity: 1;
      font-family: variables.$primaryFont;
      font-size: 1.5rem;
      line-height: 1.15;
      font-weight: 400;
      /*rtl:raw:font-family: "Zawi"*/
      @media #{variables.$vpTablet1199} {
        bottom: 1.81rem;
      }
      @media #{variables.$vpMobile600} {
        bottom: 1.78rem;
        height: auto;
      }
      span{
        color: white;
      }
    }
  }

}

.opacity-zero{
  opacity: 0;
}
.overflow-hidden{
  overflow-y: hidden;
}

.modal--elements {
  .top-element {
    position: absolute;
    top: 0;
    left: 0;
    /*rtl:raw:left: 0; right: auto;*/
  }
  .bottom-element {
    position: absolute;
    bottom: 0;
    right: 0;
    /*rtl:raw:left: auto; right: 0;*/
  }
}
.slick-track {
  margin-left: 0;
  margin-right: 0;
}
.who-we-are-section-rev,
.who-we-are-section,
.who-we-are-section-dark {
  ul {
    padding: 0 0 0 1rem;
    li {
      position: relative;
      &::before{
        content: '';
        position: absolute;
        top: 50%;
        left: -1rem;
        width: 0.35rem;
        height: 0.35rem;
        background-color: #CFA474;
        z-index: 1;
        transform: translateY(-50%) rotate(45deg);
      }
    }
  }
}
.who-we-are-section-dark {
  ul {
    li {
      color: variables.$white;
    }
  }
}