@use "../global/variables.scss";

.sec-workWithUs {
  &.sty1 {
    background: #FBF8F7;
  }
  &.sty-02 {
    background: transparent;
  }
  .leadership-wrapper {
    background: variables.$white;
    flex-direction: row-reverse;
    min-height: auto;
    @media #{variables.$vpMobile600} {
      flex-direction: column-reverse;
    }
    .content-wrapper {
      position: relative;
      padding: 2.5rem 2rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @media #{variables.$vpMobile600} {
        padding: 2rem 1.25rem;
      }
      .text-content {
        justify-content: flex-start;
        .heading3{
          color: variables.$blueSty1;
        }
        .sec-description {
          color: variables.$textStyleSecondary;
          margin-top: 1rem;
          max-width: 24rem;
          @media #{variables.$vpMobile600} {
            margin-top: 0.5rem;
          }
        }
      }
      .sec-btn {
        width: max-content;
        margin-top: 2rem;
        @media #{variables.$vpMobile600} {
          width: 100%;
        }
      }
      .leadership-art {
        @media #{variables.$vpMobile600} {
          display: none;
        }
      }
    }
    .img-box {
      padding-top: 0;
      display: flex;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}