@use "../global/variables.scss";

.company-faq-sec {
  position: relative;
  z-index: 1;
  .sub-content-sec {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.5rem;
    @media #{variables.$vpMobile600} {
      margin-bottom: 2rem;
    }
    .sub-heading {
      max-width: 30.25rem;
      @media #{variables.$vpMobile600} {
        max-width: 12.5rem;
      }
      .heading-title {
        color: variables.$blueSty1;
      }
      .special-heading {
        color: variables.$blueSty1;
        margin-bottom: 1rem;
        text-transform: uppercase;
        @media #{variables.$vpMobile600} {
          margin-bottom: 0.5rem;
        }
      }
    }
    .btn-desk {
      display: block;
      @media #{variables.$vpMobile600} {
        display: none;
      }
    }
  }
  .btn-mob {
    display: none;
    @media #{variables.$vpMobile600} {
      display: block;
      margin-top: 1.25rem;
      .sec-btn {
        display: flex;
        justify-content: center;
      }
    }
  }
  .company-faq-accordion {
    display: flex;
    column-gap: 1.5rem;
    @media #{variables.$vpMobile600} {
      flex-direction: column;
    }
    .accordion {
      flex: 0 0 49%;
      max-width: 49%;
      @media #{variables.$vpMobile600} {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .accordion-item {
        background: variables.$graySty6;
        border-radius: 12px;
        padding: 1.25rem;
        overflow: hidden;
        color: variables.$blueSty1;
        min-height: 0;
        margin-bottom: 1rem;
        @media #{variables.$vpMobile600} {
          margin-bottom: 0.5rem;
        }

        .accordion-content {
          max-height: 0;
          padding-top: 0; // Initially set padding-top to 0
          overflow: hidden;
          color: variables.$textStyleSecondary;
          transition: max-height 0.35s ease, padding-top 0.35s ease; // Apply transition to both max-height and padding-top
          p {
            color: variables.$textStyleSecondary;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.1;
            @media #{variables.$vpMobile600} {
              margin-bottom: 0.75rem;
            }
          }
        }

        .accordion-header {
          border: none;
          width: 100%;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding-bottom: 0;
          transition: all ease 0.35s;
          .accordion-heading{
            color: variables.$blueSty1;
          }

          .accordion-icon {
            min-width: 3rem;
            min-height: 3rem;
            border: 0.643px solid rgba(0, 0, 0, 0.15);
            border-radius: 50%;
            overflow: hidden;
            position: relative;
            &::after {
              content: "";
              width: 1rem;
              height: 1.5px;
              border-radius: 2px;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              background: variables.$blueSty1;
            }
            &::before {
              content: "";
              width: 1.5px;
              height: 1rem;
              border-radius: 2px;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              background: variables.$blueSty1;
              transition: all ease 0.35s;
            }
          }

          &.active {
            &.accordion-header {
              padding-bottom: 1rem;
            }
            & + .accordion-content {
              max-height: 20rem; // Set a value high enough to fit content
            }
            .accordion-icon {
              &::before {
                content: "";
                width: 1.5px;
                height: 0.875rem;
                border-radius: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                opacity: 0;
                transform: translate(-50%, -50%) rotate(90deg);
                background: variables.$blueSty1;
              }
            }
          }
        }
      }
    }
  }
}
