@use "../global/variables.scss";

.media_gallery_wrapper{
  #gallery {
    line-height:0;
    -webkit-column-count:3; 
    -webkit-column-gap:0.62rem; 
    -moz-column-count:3;
    -moz-column-gap:0.62rem;
    column-count:3;
    column-gap:0.62rem;
    @media #{variables.$vpMobile600} {
      -webkit-column-count:1; 
      -moz-column-count:1;
      column-count:1;
  }
   .img-wrap{
    position: relative;
    margin-bottom:0.62rem; 
    border-radius: 0.75rem;
    overflow: hidden;
    cursor: pointer;
    width: 100% !important;
    .box_wrapper{
      position: relative;
      a{
        display: block;
        position: relative;
        z-index: 1;
      }
      &::after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
        z-index: -1;
        @media #{variables.$vpMobile600} {
          background:  linear-gradient(180deg, rgba(0, 0, 0, 0) 49.84%, rgba(0, 0, 0, 0.8) 100%);
          z-index: 0;
        }
      }
       img{
         width: 100%;
         height: 100%;
         object-fit: cover;
       }
      
      &::after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%);
        z-index: 0;
       }
    }
    .content_box{
      display: none;
      @media #{variables.$vpMobile600} {
        position: absolute;
        z-index: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 1.5rem 2.03rem;
        width: 100%;
        bottom: 0;
        .media_title{
          color: white;
        }
        .iconBox{
          display: flex;
          width: 2.5rem;
          height: 2.5rem;
          justify-content: center;
          align-items: center;
          gap: 0.5625rem;
          border-radius: 9.375rem;
          border: 1.25px solid rgba(255, 255, 255, 0.30);
          background: rgba(199, 152, 107, 0.10);
          svg{
            width: 1rem;
            height: 1rem;
            path{
              stroke: white;
            }
          }
        }
      }
     }
     .play_icon{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
      width: 3rem;
      height: 3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 9.375rem;
      border: 1.25px solid rgba(255, 255, 255, 0.30);
      background: rgba(199, 152, 107, 0.10);
      z-index: 2;
      @media #{variables.$vpMobile600} {
        width: 2.5rem;
        height: 2.5rem;
      }
      svg{
        width: 1rem;
        height: 1rem;
      }
     }
    
     
   }
  }
}


// media gallery slider
.media-gallery-slider{
  .slick-list{
    overflow: visible;
    .slick-track{
      display: flex;
    }
  }
  .img-wrap{
    height: 23.5625rem;
    border-radius: 0.75rem;
    margin-right: .625rem;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    @media #{variables.$vpMobile600} {
      height: 18.75rem;
      margin-right: .63rem;
      max-width: 20rem; 
    }
    a{
      height: 100%;
      display: block;
      .box_wrapper{
        position: relative;
        height: 100%;
        img{
            height: 100%;
            object-fit: cover;
            width: 100%;
          }
          
          &::after{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%);
          z-index: 0;
        }
      }
      .play_icon{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
        width: 3rem;
        height: 3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 9.375rem;
        border: 1.25px solid rgba(255, 255, 255, 0.30);
        background: rgba(199, 152, 107, 0.10);
        @media #{variables.$vpMobile600} {
          width: 2.5rem;
          height: 2.5rem;
        }
        svg, img{
          width: 1rem;
          height: 1rem;
        }
      }
    }
  }
}

// video poup
.popup-overlay{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: none;
    justify-content: center;
    align-items: center;
    z-index: 100;
    @media #{variables.$vpMobile600} {
      padding: 1.25rem;
    }
    .popup-content{
      display: flex;
      width: 69.2%;
      justify-content: center;
      align-items: center;
      height: 100%;
      gap: 3.87rem;
      margin: 0 auto;
      
      @media #{variables.$vpTablet991} {
        width: 84.2%;
    }
    @media #{variables.$vpMobile600} {
      flex-direction: column;
      width: 100%;
      align-items: flex-start;
      gap: 3.06rem;
    }
      .close-btn {
        position: absolute;
        top: 5rem;
        right: 5rem;
        background: white;
        color: black;
        border-radius: 50%;
        width: 3rem;
        height: 3rem;
        text-align: center;
        font-size: 20px;
        line-height: 30px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        svg{
          width: 1.5rem;
          height: 1.5rem;
        }
      }
      .popup-left {
        flex: 1;
        @media #{variables.$vpMobile600} {
         flex: 0;
        }
        .popup-media{
          width: 34.562rem;
          height: 25.937rem;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 0.75rem;
          overflow: hidden;
          position: relative;
          @media #{variables.$vpTablet1199} {
            width: 23.562rem;
            height: 22.937rem;
          }
          @media #{variables.$vpMobile600} {
            width: 21.875rem;
            height: 16.4375rem;
          }
          video, img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%);
            z-index: 0;
          }
        }
      }
      .popup-right {
        color: white;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media #{variables.$vpMobile600} {
          flex: 0;
         }
        .popup-title {
          margin-bottom: 1rem;
          color: white;
          @media #{variables.$vpMobile600} {
            margin-bottom: 0.5rem;
          }
        }
        .popup-date {
          color: variables.$graySty3;
          opacity: 0.7;
        }
        .popup-download {
          backdrop-filter: blur(8px);
          background: rgba(199, 152, 107, .1);
          margin-top: 6.56rem;
          @media #{variables.$vpTablet1199} {
            margin-top: 3.56rem;
          }
          @media #{variables.$vpMobile600} {
            margin-top: 2rem;
            padding: 0.95rem 1.5rem;
            height: 3rem;
          }
          p{
            color: white;
            font-size: 1rem;
            line-height: 1.1;
            @media #{variables.$vpMobile600} {
              font-size: 0.875rem;
              line-height: 1;
            }
          }
          .download_iconBox{
            svg{
              path{
                stroke: white;
              }
            }
          }
          &:hover{
            background: variables.$sunriseGold;
            border: 1.25px solid hsla(0,0%,100%,.15) !important;
          }
        }
      }
      .popup-nav {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        color: white;
        display: flex;
        align-items: center;
        gap: 15px;
        .prev-btn, .next-btn {
          background: rgba(255, 255, 255, 0.2);
          border: none;
          padding: 5px 10px;
          cursor: pointer;
          border-radius: 50%;
          font-size: 18px;
          color: white;
        }
      }
    }
    .fancybox-close-small{
      position: absolute;
      top: 5rem;
      right: 5rem;
      background: white;
      color: black;
      border-radius: 50%;
      width: 3rem;
      height: 3rem;
      text-align: center;
      font-size: 20px;
      line-height: 30px;
      cursor: pointer;
      display: flex;
      opacity: 1;
      align-items: center;
      justify-content: center;
      transition: all 0.3s ease;
      
      @media #{variables.$vpTablet1199} {
        top: 3rem;
        right: 3rem;
      }
      @media #{variables.$vpMobile600} {
        top: 1.25rem;
        right: 1.25rem;
        width: 2.5rem;
        height: 2.5rem;
       }
      svg{
        width: 1.5rem;
        height: 1.5rem;
      }
      &:hover{
        background-color: variables.$sunriseGold;
        @media #{variables.$vpTablet1199} {
          background-color: white;
        }
        svg{
          path{
            fill: white;
            @media #{variables.$vpTablet1199} {
              fill: variables.$blueSty1;
            }
          }
        }
      }
    }
    &.active{
      display: flex;
    }
}

.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  border-radius: 9.375rem;
  border: 0.75px solid rgba(255, 255, 255, 0.15);
  background: rgba(199, 152, 107, 0.10);
  transition: all 0.3s ease;
  
  svg{
    width: 1rem;
    height: 1rem;
    path{
      stroke: white;
    }
  }
  &:hover{
    background: variables.$sunriseGold;
    @media #{variables.$vpTablet1199} {
      background: transparent;
    }
  }
  @media #{variables.$vpMobile600} {
    width: 2.5rem;
    height: 2.5rem;
  }
}
#prev-btn { 
  left: 5rem;
  
  @media #{variables.$vpTablet1199} {
    left: 3rem;
  }
  @media #{variables.$vpMobile600} {
    top: auto;
    bottom: 1.25rem;
    left: 1.25rem;
    transform: none;
  }
  svg{
    transform: rotate(180deg);
     /*rtl:raw:transform: rotate(0deg);*/
  }
}
#next-btn { 
  right: 5rem; 
  svg{
    /*rtl:raw:transform: rotate(180deg);*/
  }
  @media #{variables.$vpTablet1199} {
    right: 3rem;
  }
  @media #{variables.$vpMobile600} {
    top: auto;
    bottom: 1.25rem;
    right: 1.25rem;
    transform: none;
  }
}


.fancybox-infobar [data-fancybox-index] {
  font-weight: bold;
  color: white;
  opacity: 1;
}

.fancybox-infobar [data-fancybox-count] {
  font-weight: normal;
  color: rgba(255, 255, 255, 0.8);
}