@use "../global/variables.scss";
.reach-out-to-us-sec {
  padding: 9rem 0 5rem 0;
  position: relative;
  z-index: 1;
  @media #{variables.$vpMobile600} {
    padding: 5.625rem 0 4rem 0;
  }
  .title {
    margin-bottom: 1.875rem;
    color: white;
    z-index: 1;
    position: relative;
    padding: 0 5rem;
    margin-inline: auto;
    width: 100%;
    @media #{variables.$vpTablet1199} {
      padding: 0 2.5rem;
    }
    @media #{variables.$vpMobile600} {
      margin-bottom: 1rem;
      padding: 0 1.25rem;
    }
  }
  .contact-tabs {
    .tabs-nav {
      display: flex;
      align-items: center;
      column-gap: 0.5rem;
      margin-bottom: 4rem;
      padding: 0.5rem 5rem;
      margin-inline: auto;
      width: 100%;
      z-index: 2;
      @media #{variables.$vpTablet1199} {
        padding: 0 2.5rem;
      }
      @media #{variables.$vpTablet1024} {
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        padding-block: 0.3rem;
      }
      @media #{variables.$vpMobile600} {
        margin-bottom: 2.5rem;
        padding-inline-start: 1.25rem;
        padding-block: 0.3rem;
      }
      .tab-link {
        flex: 0 0 19.5%;
        max-width: 19.5%;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 0.5rem;
        padding: 1.46rem 1.5rem;
        background: rgba(199, 152, 107, 0.25);
        border-radius: 0.5rem;
        border: 1px solid rgba(255, 255, 255, 0.15);
        overflow: hidden;
        color: white;
        cursor: pointer;
        text-transform: uppercase;
        z-index: 1;
        transition: all ease 0.35s;
        @media #{variables.$vpTablet1024} {
          min-width: 10.625rem;
          width: 100%;
        }
        @media #{variables.$vpMobile600} {
          padding: 1rem 1.5rem;
        }
        .tab-title {
          transition: all ease 0.35s;
          white-space: nowrap;
          letter-spacing: 0.64px;
        }
        &:hover {
          background: variables.$sunsetGoldSty2;
          @media #{variables.$vpTablet1199} {
            background: rgba(199, 152, 107, 0.25);
          }
        }
        &.active {
          background: variables.$sunsetGoldSty2;
          .tab-title {
            position: relative;
            transform: translateX(8px);
            &::before {
              position: absolute;
              content: url("/assets/img/icon/diamondIcon.svg");
              width: 0.5rem;
              height: 100%;
              left: -1rem;
              top: 50%;
              transform: translateY(-50%);
              opacity: 1;
              scale: 1;
              animation: iconAppear 0.35s ease forwards;
            }
          }
        }
      }
    }
    .forms-container {
      position: relative;
      z-index: 1;
      min-height: 40.25rem;
      .contact-form {
        display: none;
        &.active {
          display: block;
        }
        .primary-fields {
          display: flex;
          flex-wrap: wrap;
          column-gap: 1.25rem;
          row-gap: 1.5rem;
          @media #{variables.$vpMobile600} {
            row-gap: 1rem;
          }
          .form-group {
            flex: 0 0 49.2%;
            max-width: 49.2%;
            @media #{variables.$vpTablet1199} {
              flex: 0 0 48.9%;
              max-width: 48.9%;
            }
            @media #{variables.$vpMobile600} {
              flex: 0 0 100%;
              max-width: 100%;
            }
          }
        }
        .submit-btn {
          margin-top: 1.5rem;
          .btn {
            width: 11.125rem;
            height: 2.5rem;
            justify-content: center;
            border: 1.25px solid rgba(255, 255, 255, 0.3);
            @media #{variables.$vpMobile600} {
              width: 100%;
              height: 1.99625rem;
            }
          }
          @media #{variables.$vpMobile600} {
            margin-top: 1rem;
          }
        }
        .error-message {
          display: none;
          color: #eb5860;

          &.show {
            display: block;
          }
        }
        .form-group {
          display: flex;
          flex-direction: column;
          .input-label {
            margin-bottom: 0.25rem;
            color: white;
          }
          .input-txt-field {
            resize: none;
            border-radius: 0.625rem;
            border: 1px solid rgba(255, 255, 255, 0.15);
            overflow: hidden;
            background: transparent;
            padding: 1.07rem 0.75rem;
            color: variables.$graySty3;
            width: 100%;
            /*rtl:raw:text-align: right;*/
            font-family: variables.$secondaryFont;
            &:focus-visible {
              outline: none;
            }
            @media #{variables.$vpMobile600} {
              padding: 0.89rem 0.75rem;
            }
          }
          &.txt-area-body {
            margin-top: 1.5rem;
            @media #{variables.$vpMobile600} {
              margin-top: 1rem;
            }
            .input-txt-field {
              min-height: 8.9rem;
              padding: 1.07rem 0.75rem;
              border-radius: 0.625rem;
              border: 1px solid rgba(255, 255, 255, 0.15);
              overflow: hidden;
              background: transparent;
              padding: 0.8125rem 0.75rem;
              color: variables.$graySty3;
              font-family: variables.$secondaryFont;
              height: 12rem;
              @media #{variables.$vpMobile600} {
                height: 8.6875rem;
              }
            }
            .char-count {
              color: variables.$graySty2;
              text-align: right;
              margin-top: 0.5rem; 
            }
          }
        }
        .iti__flag-container {
          /*rtl:raw:right: 0.75rem !important;*/
          /*rtl:raw:left: auto !important;*/
          .iti__selected-flag {
            background: transparent;
            // .iti__flag {
            //   width: 1.5rem;
            //   height: 1.5rem;
            //   border-radius: 50%;
            //   overflow: hidden;
            // }
            .iti__selected-dial-code {
              margin-left: 0.5rem;
              font-size: 1.125rem;
              font-weight: 400;
              line-height: 1.1;
              color: white;
              @media #{variables.$vpMobile600} {
                font-size: 1rem;
              }
            }
            .iti__arrow {
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background: url("/assets/img/icon/dropdown-icon.svg");
                background-position: center;
              }
            }
            .iti__arrow--up {
              transform: rotate(180deg);
              border-bottom: 0;
              margin-top: 0;
              background-position: center;
            }
          }
          .iti__country-list {
            border-radius: 0.75rem;
            background: rgba(255, 255, 255, 0.8);
            backdrop-filter: blur(12px);
            .iti__country {
              color: black;
              .iti__flag-box {
              }
              .iti__country-name {
              }
              .iti__dial-code {
                color: black;
              }
            }
          }
        }
      }
    }
  }
}
@keyframes iconAppear {
  0% {
    opacity: 0;
    transform: translateY(-50%) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translateY(-50%) scale(1);
  }
}

.iti--allow-dropdown .iti__flag-container:hover .iti__selected-flag {
  background: transparent !important;
}
.error-message,
.success-message {
  color: variables.$white !important;
  display: none !important;
}
.pop--wrapper {
  .popup-box {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #FFF;
    padding: 3.5rem 2.5rem;
    z-index: 5;
    width: 37.625rem;
    left: 50%;
    border-radius: 1rem;
    opacity: 0;
    visibility: hidden;
    .postBox {
      position: relative;
      z-index: 2;
    }
    @media #{variables.$vpMobile600} {
      width: 80%;
    }
    .popup-para {
      padding: 1.5rem 0 2rem;
    }
    .bg-elements {
      .top-element {
        position: absolute;
        top: 0;
        left: 0;
      }
      .bottom-element {
        position: absolute;
        bottom: 0;
        right: 0;
      }
    }
  }
}
.form--wrapper {
  &.formSuccess {
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(variables.$black, $alpha: 0.5);
      opacity: 1;
      transform: none;
      transition: none;
    }
    .success-popup {
      opacity: 1;
      visibility: visible;
    }
  }
  &.formfailed {
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(variables.$black, $alpha: 0.5);
      opacity: 1;
      transform: none;
    }
    .failer-popup {
      opacity: 1;
      visibility: visible;
    }
  }
  .cross {
    width: 3rem;
    height: 3rem;
    position: absolute;
    top: 1rem;
    right: 1rem;
    border-radius: 2.65181rem;
    border: 0.643px solid rgba(0, 0, 0, 0.15);
    background: #FFF;
    cursor: pointer;
    transition: all 0.35s ease;
    z-index: 2;
    img,svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &:hover {
      border: 1.25px solid hsla(0, 0%, 100%, .15);
      background: var(--action-primary-hover, #aa865e) !important;
      svg {
        path {
          stroke: variables.$white;
        }
      }
    }
  }
}
.form-group {
  &.success {
    position: relative;
    .input-txt-field {
      border-color: variables.$success !important;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0.75rem;
      top: 2.3rem;
      background: url(/assets/img/icon/success.png);
      background-size: contain;
      background-repeat: no-repeat;
      width: 1.5rem;
      height: 1.5rem;
      @media #{variables.$vpMobile600} {
        top: 2rem;
      }
    }
    .success-message {
      display: block !important;
    }
  }
  &.failed {
    position: relative;
    .input-txt-field {
      border-color: variables.$failed !important;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0.75rem;
      top: 2.3rem;
      background: url(/assets/img/icon/alert.png);
      background-size: contain;
      background-repeat: no-repeat;
      width: 1.5rem;
      height: 1.5rem;
      @media #{variables.$vpMobile600} {
        top: 2rem;
      }
    }
    .error-message {
      display: block !important;
    }
  }
}
input:-internal-autofill-selected {
  background: red !important;
}