@use "../global/variables.scss";

.inner_404_banner{
    position: relative;
    height: 100vh;
    width: 100%;
    z-index: 1;
  @media #{variables.$vpMobile600} {
    height: 100vh;
  }
  .contentWrapper{
    justify-content: center;
    text-align: center;
    display: flex;
    height: 100%;
    .text-container{
      max-width: 100%;
      display: flex;
      justify-content: center;
      overflow: visible;
      flex-direction: column;
      align-items: center;
      margin-bottom: 0;
      height: 100%;
      .main_title_box{
        position: relative;
        width: 15.5625rem;
        color: variables.$blueSty1;
        padding-bottom: 0.81rem;

        @media #{variables.$vpMobile600} {
          width: 7.34181rem;
        }

        .bottom-active {
          width: 100%;
          background-color: rgba(169, 125, 70, 1);
          height: 0.5px;
          position: absolute;
          bottom: 0;
          left: 0;
          transition: width 0.3s ease, background-color 0.3s ease;
          svg{
            width: 0.625rem;
            height: 0.62363rem;
            path{
              fill: rgba(169, 125, 70, 1);
            }
          }
          
          .icon-start {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -2px;
          }
          .icon-center {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 10%;
            @media #{variables.$vpMobile600} {
              left: 14%;
            }
          }
          .icon-end {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 38%;
            @media #{variables.$vpMobile600} {
              left: 50%;
            }
          }
        }
      }
      .inner_content_box{
        display: flex;
        justify-content: center;
        gap: 1.5rem;
        flex-direction: column;
        align-items: center;
        margin-top: 1.529rem;
        color: variables.$blueSty1;
        @media #{variables.$vpMobile600} {
          gap: 1rem;
          margin-top: 1.31rem;
        }
        .body1{
          max-width: 25.375rem;
          @media #{variables.$vpMobile600} {
            max-width: 16.5rem;
          }
        }
        .btn_wrap{
          padding-top: 1rem;
        }
      }
    }
  }
}