@use './variables.scss';

.btn {
    border-radius: 9.375rem;
    padding: 2px 1.5rem 0 1.5rem;
    border: 0.75px solid rgba(0, 0, 0, 0.15);
    background: rgba(199, 152, 107, 0.10);
    color: variables.$blueSty1;
    letter-spacing: 0.04rem;
    font-size: 1rem;
	font-weight: bold;
	line-height: 1.15;
    height: 3rem;
    width: max-content;
    align-items: center;
    display: flex;
    text-transform: uppercase;
    transition: all 0.5s ease;
    cursor: pointer;
    font-family: variables.$secondaryFont;
	@media #{variables.$vpMobile600} {
		font-size: 0.875rem;
        justify-content: center;
        height: 2.5rem;
        padding: 0 1rem;
	}
    &.btn-transparent {
        background: transparent;
    }
    &.btn-secondary {
        background: transparent;
        border: 0.75px solid rgba(227, 227, 228, 0.15);
    }
    &.btn-primary {
        background: rgba(199, 152, 107, 0.10);
        border:  0.75px solid rgba(255, 255, 255, 0.15);
    }
    &.btn-dark {
        color: variables.$white;
        @media #{variables.$vpTablet1199} {
            &:hover {
                color: variables.$white !important;
            }
        }
    }
    &.btn_Icon{
        display: flex;
        justify-content: center;
        gap: 0.5625rem;
        background: transparent;
        svg{
            width: 1rem;
            height: 1rem;
        }
    }
    &.no--bg{
        background: transparent;
        border: 0.75px solid rgba(0, 0, 0, 0.15);
    }
    &:hover {
        border-color: rgba(255, 255, 255, 0.15);
        background: var(--action-primary-hover, #AA865E) !important;
        color: variables.$white !important;
        @media #{variables.$vpTablet1199} {
            border: 0.75px solid rgba(0, 0, 0, 0.15);
            background: rgba(199, 152, 107, 0.10) !important;
            color: variables.$blueSty1 !important;
        }
        &.btn-secondary {
            background: transparent;
            border-color: rgba(227, 227, 228, 0.30);
        }
    }
}